// import { RECEIVE_HELLO_WORLD } from "../actions/actions";
import * as actionTypes from '../actions/actionTypes';

import { LOGGED_IN, LOGGED_OUT, LOGIN_CANCELLED, LOGIN_ERROR } from './statusTypes';
// import initialState from './initialState';

const initialState = {
  login: {
    token: null,
    status: 'logged out',
    spinning: false,
    username: localStorage.getItem('username'),
    imageids:[],
    texting:[]
  },
}

export default function userRole(state = initialState.login, action) {
  let newState;
  switch (action.type) {
    case actionTypes.LOGIN_SUCCESS:
      newState = {...state, status: LOGGED_IN, spinning:true};
      return newState;
    case actionTypes.SAVE_TOKEN:
      newState = {...state, token: action.token,spinning:false,username:action.username};
      newState.token = action.token;
      return newState;
    case actionTypes.GLOBAL_IMAGES_SUCCESS:
      newState = {...state, imageids: action.imgtask};
      newState.imageids = action.imgtask;
      return newState;
    case actionTypes.GLOBAL_TEXT_SUCCESS:
      newState = {...state, texting: action.texttask};
      newState.texting = action.texttask;
      return newState;
    case actionTypes.GLOBAL_IMG_DELETE_SUCCESS:
      newState = {...state, imageids: action.task};
      newState.imageids = action.task;
      return newState;
    case actionTypes.DELETE_TOKEN:
      newState = {...state, token: null};
      newState.token = null;
      return newState;
    case actionTypes.LOGOUT:
      newState = {...state, status: LOGGED_OUT};
      return newState;
    case actionTypes.LOGIN_ERROR:
      newState = {...state, status: LOGIN_ERROR};
      return newState;
    case actionTypes.LOGIN_CANCELLED:
      newState = {...state, status: LOGIN_CANCELLED};
      return newState;
    default:
      return state;
  }
}