import { applyMiddleware, compose, createStore } from 'redux';
import createSagaMiddleware from 'redux-saga';
import thunk from 'redux-thunk';
import rootReducer from '../reducers/index';
import { logActions, loginFlow, imageFlow, textFlow, DeleteImgFlow } from '../saga/sagas';


export default function configureStore() {
  const sagaMiddleware = createSagaMiddleware()
  const composeArgs = [
    applyMiddleware(sagaMiddleware),
    applyMiddleware(thunk),
  ];
  if(window && window.__REDUX_DEVTOOLS_EXTENSION__) {
    composeArgs.push(window.__REDUX_DEVTOOLS_EXTENSION__());
  }

  const store = createStore(
    rootReducer,
    compose.apply(undefined, composeArgs)
  );

  sagaMiddleware.run(loginFlow);
  sagaMiddleware.run(logActions);
  sagaMiddleware.run(imageFlow);
  sagaMiddleware.run(textFlow);
  sagaMiddleware.run(DeleteImgFlow);
  

  return store;
}