import React from "react";
import { connect } from "react-redux";
import { GLOBAL_IMAGES, GLOBAL_TEXT, GLOBAL_IMG_DELETE } from '../../actions/actionTypes';
// reactstrap components
import {
  Card,
  Container,
  Button,
  Row,
  FormGroup,
  Col,
  Form,
  // Badge,
  // CardFooter,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  InputGroup,
  CardHeader,
  CardBody,
  Label,
  CustomInput,
  Table,
  UncontrolledAlert
} from "reactstrap";
import axios from "axios";
import Header from "components/Headers/HeaderCustom.js";
import GallaryImages from "views/examples/subcomponents/GallaryImages.js";
class LiveGallary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      counter: "",
      images: [],
      texts: [],
      multirefiles: '',
      multiexcels: '',
      author: "kiran",
      checkedItems: new Map(),
      datasourcetype: "text",
      datalang: "",
      textboxdata: "",
      isProcessing: false,
      alertcolor: null,
      isalert: false,
      alertmsg: '',
      multiimg: '',
      allchecks: [],
      texting: [],
      textsendmsg: '',
      imagesendmsg: ''
    };
  }

  fetchArticles = () => {
    axios
      .get(`http://localhost:8099/livefilepost`)
      .then((res) => {
        this.setState({
          images: res.data.response,
          texts: res.data.result
        });
      });
  };

  componentDidMount() {
    this.fetchArticles();


  }
  toSubmitLive = async (e) => {
    var allchecks = [];
    let i;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (i of allchecks) {
      if (i.checked === true) {
        this.state.allchecks.push(i.id)
      }

    }


    await this.props.imageglobfetch(this.state.allchecks);
    this.setState({
      imagesendmsg: 'added'

    });

  }



  toRemoveLive = async (e) => {
    var allchecks = [];
    let i;
    let m;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (m of allchecks) {
      if (m.checked === true) {
        this.state.allchecks.push(m.id)
        let imagealter = parseInt(m.id)
        const images = this.state.images.filter(i => i.id !== imagealter);
        this.setState({ images });

      }


    }


    await this.props.imageglobdelete(this.state.allchecks);




  }

  toSubmitLivetext = async (e) => {



    await this.props.textglobfetch(e.target.id);
    this.setState({
      textsendmsg: 'added'

    });


  }
  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value

    });
  }

  onFileChange = (e) => {
    this.setState({ "multiimg": e.target.files })
  }

  onFilexlChange = (e) => {
    this.setState({ "multiexcels": e.target.files })
  }

  bulkprocessor = async (e) => {
    var allchecks = [];
    let i;
    allchecks = document.getElementsByName('allchildren');
    if (e.target.checked === true) {

      // document.getElementById('idhere').checked = false;
      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = true;

      }

    }
    else {

      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = false;

      }

    }
  }
  bulkprocessor2 = async (e) => {
    var allchecks = [];
    let i;
    allchecks = document.getElementsByName('allchildrentext');
    if (e.target.checked === true) {

      // document.getElementById('idhere').checked = false;
      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = true;

      }

    }
    else {

      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = false;

      }

    }

  }

  handleImageSubmit = (e) => {


    let form_data = new FormData();
    console.log(this.state.multiimg);
    for (const key of Object.keys(this.state.multiimg)) {
      form_data.append('image', this.state.multiimg[key])
      // this.state.imagesnore.push((this.state.multirefiles[key].name).replace(" ", "_"))

    }
    let url = 'http://localhost:8099/livepost';
    const config = {
      headers: { 'content-type': 'multipart/form-data' }
    }
    this.setState(state => ({
      isProcessing: !state.isProcessing
    }));
    axios.post(url, form_data, config)
      .then(res => {
        console.log(res.data);
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'success',
          isalert: true,
          alertmsg: 'Great Job Images Created Successfully, Check Folders'

        }));
      })
      .catch(err => {
        alert(err)
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'danger',
          isalert: true,
          alertmsg: 'Something Went Wrong Check Form Fields'
        }));

      })

  }

  handleExcelSubmit = (e) => {


    let form_data = new FormData();
    form_data.append('language', this.state.datalang);
    form_data.append('sourcetype', this.state.datasourcetype)

    if (this.state.datasourcetype === "excel") {
      for (const key of Object.keys(this.state.multiexcels)) {
        form_data.append('excel', this.state.multiexcels[key])

      }
    }
    else {
      form_data.append('textboxdata', this.state.textboxdata)
    }


    let url = 'http://localhost:8099/livefilepost';
    const config = {
      headers: { 'content-type': 'multipart/form-data' }
    }
    this.setState(state => ({
      isProcessing: !state.isProcessing
    }));
    axios.post(url, form_data, config)
      .then(res => {
        console.log(res.data);
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'success',
          isalert: true,
          alertmsg: 'Great Job Images Created Successfully, Check Folders'

        }));
      })
      .catch(err => {
        alert(err)
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'danger',
          isalert: true,
          alertmsg: 'Something Went Wrong Check Form Fields'
        }));

      })


  }

  handleSubmit(e) {
    e.preventDefault();

    let truebox = [];

    // for (let [key,value] of Object.entries(allchecks)) {
    //     console.log(key,value.);
    // }
    let allchecks = document.getElementsByName("allchildren");
    Object.entries(allchecks).filter(([key, val]) => {
      if (val.checked === true) {
        truebox.push(val.id);
      }
      return truebox;
    });
    console.log(truebox);
    // this.setState(state => ({
    //     isProcessing: !state.isProcessing
    //   }));
    // const config = {
    //     headers: { 'Content-Type': 'application/json' }
    // }
    // axios.post(`http://localhost:8099/video/${this.state.author}/${this.state.numiterations}`,form_data,config).then(res => {
    //     console.log(form_data);
    //     this.setState(state => ({
    //         isProcessing: !state.isProcessing
    //       }));
    // });
  }

  handleSubmittext = (e) =>
  {
    e.preventDefault();

    let truebox = [];
    let form_data = new FormData(); 

    this.setState(state => ({
        isProcessing: !state.isProcessing
      }));

    const config = {
        headers: { 'Content-Type': 'application/json' }
    }
    var allchecks = [];
    let i;
    allchecks = document.getElementsByName('allchildrentext');


    // document.getElementById('idhere').checked = false;
    for (i of allchecks) {
      if (i.checked === true) {
        truebox.push(i.id)
        let alter = parseInt(i.id)
        const texts = this.state.texts.filter((m) => m.id !== alter);
        this.setState({ texts });
      }

    }


    form_data.append('textlist',truebox.toString())

    axios.post(`http://localhost:8099/deletetexts`,form_data,config).then(res => {        
        this.setState(state => ({
            isProcessing: !state.isProcessing
          }));

        
    });

  }

  render() {

    const textd = this.state.texts.map((item, key) => {
      return (
        <tr key={item.id}>
          <td>
                    <div className="custom-control custom-control-alternative custom-checkbox">
                    <input
                    className="custom-control-input"
                    type="checkbox"
                    id={item.id}
                    name='allchildrentext' checked={this.state.checkedItems.get(item.id)} 
                    onChange={this.handleChange}
                  />
                  <label
                    className="custom-control-label"
                    htmlFor={item.id}
                    style={{marginBottom:"12px"}}
                  >
                    
                  </label>
                    </div>
                  
                </td>

          <td>
            <Input

              type="textarea"
              id={item.id}
              rows="10"
              value={item.data}
              disabled={true}
              onChange={this.handleChange}

            ></Input>
          </td>
          <td> <Button
            className="my-2"
            color="info"
            type="button"
            id={item.id}
            onClick={this.toSubmitLivetext}
          >
            Send Text To Live
              </Button>
          </td>




        </tr>



      )
    })
    return (
      <>
        <Header />
        <Container className="mt--7" fluid>
          <Row>
            <Col>
              <Card className="shadow border-0">
                <CardBody>
                  <Form role="form">
                    <Row>
                      <Col md="10">
                        <FormGroup className="mb-3">
                          <Label for="multiimg">
                            Upload Background Images
                          </Label>
                          <InputGroup className="input-group-alternative">
                            <CustomInput
                              type="file"
                              id="multiimg"
                              name="multiimg"
                              onChange={this.onFileChange}
                              multiple
                              label="Yo, pick a files!"
                            />
                          </InputGroup>
                        </FormGroup>
                      </Col>
                      <Button
                        disabled={this.state.isProcessing}
                        onClick={this.handleImageSubmit}
                        className="my-4"
                        color="primary"
                        type="button"
                      >
                        Push
                      </Button>
                      {this.state.isalert ?
                        <UncontrolledAlert className={`alert-${this.state.alertcolor}`} fade={true}>
                          <span className="alert-inner--icon">
                            <i className="ni ni-like-2" />
                          </span>{" "}
                          <span className="alert-inner--text">
                            <strong>Status !</strong> {this.state.alertmsg}
                          </span>
                        </UncontrolledAlert>
                        :
                        null
                      }
                    </Row>
                  </Form>
                  <hr></hr>
                  <Form role="form">
                    <Row>
                      <Col md="12">
                        <FormGroup className="mb-3">
                          <Label>Slect Language</Label>
                          <InputGroup className="input-group-alternative">
                            <Input
                              type="select"
                              name="datalang"
                              id="datalang"
                              value={this.state.datalang}
                              onChange={this.handleChange}
                            >
                              <option value="">Select Language</option>
                              <option value="eng">English</option>
                              <option value="tel">Telugu</option>
                              <option value="hindi">Hindi</option>
                            </Input>
                          </InputGroup>
                        </FormGroup>

                        <FormGroup className="mb-3">
                          <Label>Data Source Type</Label>
                          <InputGroup className="input-group-alternative">
                            <Input
                              type="select"
                              name="datasourcetype"
                              id="datasourcetype"
                              value={this.state.datasourcetype}
                              onChange={this.handleChange}
                            >
                              <option value="">Select Source Type</option>
                              <option value="excel">Excel</option>
                              <option value="text">Text</option>
                            </Input>
                          </InputGroup>
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      {this.state.datasourcetype === "excel" ? (
                        <Col md="8">
                          <FormGroup className="mb-3">
                            <Label for="multiexcels">
                              Upload Excel
                            </Label>
                            <InputGroup className="input-group-alternative">
                              <CustomInput
                                type="file"
                                id="multiexcels"
                                name="multiexcels"
                                onChange={this.onFilexlChange}
                                multiple
                                label="Yo, pick a files!"
                              />
                            </InputGroup>
                          </FormGroup>
                        </Col>
                      ) : (
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Place Text By Enter : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="textarea"
                                  name="textboxdata"
                                  id="textboxdata"
                                  placeholder="Your Data Categorized by Enter"
                                  rows="10"
                                  value={this.state.textboxdata}
                                  onChange={this.handleChange}
                                ></Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                        )}

                    </Row>
                    <Button
                      disabled={this.state.isProcessing}
                      onClick={this.handleExcelSubmit}
                      className="my-4"
                      color="primary"
                      type="button"
                    >
                      Submit
                    </Button>
                    {this.state.isalert ?
                      <UncontrolledAlert className={`alert-${this.state.alertcolor}`} fade={true}>
                        <span className="alert-inner--icon">
                          <i className="ni ni-like-2" />
                        </span>{" "}
                        <span className="alert-inner--text">
                          <strong>Status !</strong> {this.state.alertmsg}
                        </span>
                      </UncontrolledAlert>
                      :
                      null
                    }
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col>
              <Card className="shadow border-0">
                <CardBody>
                <h3>{this.state.imagesendmsg}</h3>
                  <Table className="align-items-center table-flush" responsive>
                    
                    <thead className="thead-light">
                      
                      <tr>
                        <th scope="col">
                          <CustomInput type="checkbox" name="parentcheckbox" id="selectall" onChange={this.bulkprocessor} inline />
                        </th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col">Source</th>
                        <th scope="col">
                          <Button
                            className="my-2"
                            color="primary"
                            type="button"
                            onClick={this.toSubmitLive}
                          >
                            Send Images
                          </Button>
                        </th>
                        <th scope="col">
                          <Button className="my-2" color="danger" type="button" onClick={this.toRemoveLive}>
                            Remove Images
                          </Button>
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <GallaryImages

                        checkedItems={this.state.checkedItems}
                        ims={this.state.images}
                        handleChange={this.handleChange}
                      />
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col>
              <Card className="shadow border-0">
                <CardBody>
                <h3>{this.state.textsendmsg}</h3>
                  <Table className="align-items-center table-flush" responsive>
                    
                    <thead className="thead-light">
                      <tr>
                        <th>
                        <CustomInput type="checkbox" name="parentextbox" id="parentselector" onChange={this.bulkprocessor2} inline />
                        </th>
                        <th scope="col">
                          data
                        </th>
                        
                        <th scope="col">Source</th>
                        <th scope="col">
                        <Button className="my-2" color="danger" type="button" onClick={this.handleSubmittext}>
                            Remove Text
                          </Button>

                        </th>

                      </tr>
                    </thead>
                    <tbody>
                      {textd}
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
    imageids: state.login.imageids,
    texting: state.login.texting,
    username: state.login.username
  };
}

const mapDispachToProps = dispatch => {
  return {
    imageglobfetch: (data) => dispatch({ type: GLOBAL_IMAGES, imageids: data }),
    textglobfetch: (data) => dispatch({ type: GLOBAL_TEXT, texting: data }),
    imageglobdelete: (data) => dispatch({ type: GLOBAL_IMG_DELETE, imageids: data, act: 'delroot' }),
  };
}
export default connect(mapStateToProps, mapDispachToProps)(LiveGallary);
