export function* authorize(username, password) {
  try {
    const token = yield call(loginUserService, username, password)    
    yield put({type: 'LOGIN_SUCCESS'})
    if(token){
      yield put({type: 'SAVE_TOKEN', token,username});
    }    
    
  } catch(error) {
    yield put({type: 'LOGIN_ERROR', error})
  }
  finally {
    if (yield cancelled()) {
      yield put({type: 'LOGIN_CANCELLED'})
    }
  }
}

 

export function* imageFlow() {
  while (true) {
    const {imageids}= yield take('GLOBAL_IMAGES');
    const imgtask = yield call(getImageService,imageids)    
    if(imgtask){
      yield put({type: 'GLOBAL_IMAGES_SUCCESS', imgtask});
    }
  }
}
export function* textFlow() {
  while (true) {
    const {texting}= yield take('GLOBAL_TEXT');
    const texttask = yield call(getTextService,texting)    
    if(texttask){
      yield put({type: 'GLOBAL_TEXT_SUCCESS', texttask});
    }
  }
}

export function* DeleteImgFlow() {
  while (true) {
    const {imageids,act}= yield take('GLOBAL_IMG_DELETE');
    const task = yield call(DelImgService,imageids,act)
    console.log('its saga copied',task);
    if(task){
      yield put({type: 'GLOBAL_IMG_DELETE_SUCCESS', task});
    }
  }
}
export function* loginFlow() {
  while (true) {  
    
    const {user, password} = yield take('LOGIN_REQUEST')    
    const task = yield fork(authorize, user, password)
    const action = yield take(['LOGOUT', 'LOGIN_ERROR'])
    if (action.type === 'LOGOUT') {
      yield cancel(task)
      yield put({type: 'DELETE_TOKEN'})
    }
  }
}


export function* logActions() {
  while (true) {
    const action = yield take('*')
    console.log(action.type);
  }
}