import React from "react";
import './livedash.css';
import { Input, Row, Col,Label } from "reactstrap";
import { connect } from "react-redux";
// core components
// import { fadeIn,bounce  } from 'react-animations'
// import Radium, {StyleRoot} from 'radium';
// import Animate from 'animate.css-react'










class LiveDashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      counter: this.props.texting,
      images:  this.props.imageids,
      textval: "Welcome to Leve Stream",
      imagereturn: "static/livestreams/images/21_(3).jpg",
      animclass: 'showingbg',
      textpad: '25% 25% 25% 25%',
      textfontsize: '250%',
      textfontfamily: 'Ariel',
      textalignment: 'center',
      textfontcolor:'#ffffff',
      textfontweight:'normal',
      textfontwidth:'800px',
      divshow:true,
      countoftext:0,
      countofimages:0

    }
  }


  determineTextval(){
    if (this.state.countoftext < (((this.props.texting).length)-1)) {
      this.setState((state) => ({
        countoftext: state.countoftext + 1        
                // animclass: state.animclass === 'show' ? 'hide':'show'
      }));
     return this.state.countoftext
    }
    else {
      this.setState((state) => ({
        countoftext: 1        
                // animclass: state.animclass === 'show' ? 'hide':'show'
      }));
     return this.state.countoftext

    }    
    
  }

  determineImageval(){
    if (this.state.countofimages < (((this.state.images).length)-1)) {
      this.setState((state) => ({
        countofimages: state.countofimages + 1
      }));
     return this.state.countofimages
    }
    else {
      this.setState((state) => ({
        countofimages: 0       
        
      }));
     return this.state.countofimages

    }    
    
  }

  myFunction() {
    if (((this.props.imageids).length) > 1) {
      setInterval(() => {
        this.setState((state) => ({
          // animclass: state.animclass === 'fadeing' ? 'fadeouting':'fadeing',
          textval: state.counter[this.determineTextval()],
          imagereturn: this.props.imageids[this.determineImageval()],
          
        }));
      },5000);

    }
    else{
      setInterval(() => {
        this.setState((state) => ({
          // animclass: state.animclass === 'fadeing' ? 'fadeouting':'fadeing',
          textval: state.counter[this.determineTextval()],          
          
        }));
      },5000);

    }
    


  }

  componentDidMount() {
    this.myFunction();
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value

    });
  }

  handleClick = (e) => {
    this.setState({
      divshow: !this.state.divshow

    });
  }
  



  render() {

    const text = {
      t: {


        textAlign: this.state.textalignment,
        color: this.state.textfontcolor,
        fontSize: this.state.textfontsize,
        margin: this.state.textpad,
        fontFamily: this.state.textfontfamily,
        fontWeight: this.state.textfontweight,
        width:this.state.textfontwidth,
        zIndex:'99999',
        position:'absolute'
        
        
        



      }

    }


    return (
      <>
      <button className='text-centerforimg' onClick={this.handleClick}></button>
        <Row className='text-centerforimg' style={{display:this.state.divshow ? 'block':'none'}}>
          <Col md="6">
            <Label for="textalignment"> Text Align</Label>
            <Input id="textalignment" type="select" value={this.state.textalignment} onChange={this.handleChange}>
            <option value=''>Select Text Alignment</option>
                                                    <option value="center">center</option>
                                                    <option value="left">Left</option>
                                                    <option value="right">Right</option>
                                                    <option value="justify">Justify</option>
                                                    </Input>
            <Label for="textfontsize"> Text Font Size</Label>
            <Input value={this.state.textfontsize} id="textfontsize" type="text" onChange={this.handleChange}></Input>
            <Label for="textpad"> Top Right Bottom Left</Label>
            <Input value={this.state.textpad} id="textpad" type="text" onChange={this.handleChange}></Input>
            <Label for="textfontweight"> Font weight in Number or normal, Bold</Label>
            <Input value={this.state.textfontweight} id="textfontweight" type="text" onChange={this.handleChange}></Input>
            <Label for="textfontwidth"> Text Width</Label>
            <Input value={this.state.textfontwidth} id="textfontwidth" type="text" onChange={this.handleChange}></Input>
            
            <Label for="textfontfamily"> Font Family</Label>
            <Input type="select" value={this.state.textfontfamily} id="textfontfamily" onChange={this.handleChange}>
                                                    <option value=''>Select Font Style</option>
                                                    <option value="Ramabhadra">RamaBhadra</option>
                                                    <option value="Rubik">Rubik</option>
                                                    <option value="Damion">Damion</option>
                                                    <option value="Verdana">Verdana</option>
                                                    <option value="Times New Roman">Times New Roman</option>
                                                    <option value="Century Gothic">Century Gothic</option>
                                                    <option value="Oswald">Oswald</option>                                                    
                                                    <option value="Courier New">Courier New</option>
                                                    <option value="serif">serif</option>
                                                    <option value="sans-serif">sans-serif</option>
                        </Input>
                        <Label for="textfontcolor"> Font Color</Label>
            <Input value={this.state.textfontcolor} id="textfontcolor" type="color" onChange={this.handleChange}></Input>
          </Col>
        </Row>
        
        

          <div style={text.t} className={this.state.animclass}>{this.state.textval}</div>

          <div id="cf6_image" style={{ backgroundImage: `url('http://localhost:8099/${this.state.imagereturn}')` }}>
            
        </div>







      </>
    );
  }
}


const mapStateToProps = (state, ownProps) => {
  return {
      //   token: state.login.token,
      //   status: state.login.status,
      //   spinning: state.login.spinning,
      username: state.login.username,
      imageids:state.login.imageids,
      texting:state.login.texting
      
  };
}

export default connect(mapStateToProps)(LiveDashboard);