
import React from "react";
import axios from "axios";
import { connect } from "react-redux";
import classnames from "classnames";

// reactstrap components
import {
  Card, Container, Button, Row, FormGroup,
  Form,
  // Badge,
  // CardFooter,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  InputGroup,
  CardHeader,
  CardBody,
  Label,
  CustomInput,
  CardImg,
  Alert,
  Col,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  // UncontrolledTooltip,
  // Media,
  // Pagination,
  // PaginationItem,
  // PaginationLink,
  // Progress,
  // Table,
  // DropdownMenu,
  // DropdownItem,
  // UncontrolledDropdown,
  // DropdownToggle,
  Spinner
} from "reactstrap";

import StyledDropzone from "views/examples/subcomponents/uploadform.js"
// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}

class TelegramApi extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: 'group',
      author: this.props.username,
      tabs: 1,
      groupdata: [],
      channeldata: [],
      isProcessing: false,
      multirefiles: '',
      alertcolor: null,
      isalert: false,
      alertmsg: '',
      textboxdata: '',
      sendnowtitle: 'group',
      groupdatatab2: '',
      masgtyptab2: 'textmessage',
      scheduletitle: 'group',
      timenow: new Date().getHours(),
      endtime: 23,
      gap: 900,
      multirefiles_tab3: '',
      multirefilesimages_tab3: '',
      maxtime: 10,
      checkedItems: new Map(),
      gpdata: '',
      chdata: ''


    };
  }


  fetchArticles = async () => {
    await axios.get("http://localhost:8099/telegram/add/").then(res => {

      this.setState({
        groupdata: res.data.group,
        channeldata: res.data.channels,
      });
    });
    this.setState({ "gpdata": this.state.groupdata.map((item, key) => item.val) })
    this.setState({ "chdata": this.state.channeldata.map((item, key) => item.val) })
  }

  toggleNavs = (e, state, index) => {
    e.preventDefault();
    this.setState({
      [state]: index
    });
  };

  componentDidMount() {
    this.fetchArticles();



    const token = localStorage.getItem('token');
    console.log(token)
    if (!token) {
      this.props.history.push('login');
      // this.props.history.push('register');       

    }
    else {
      this.props.history.push('telegram');

      //  console.log(this.props)    

    }

  }

  // getfilename = (e)=>{
  //   return e;
  // }

  handleDelete = (item) => {

    const articles = this.state.articles.filter(i => i.id !== item.id)
    this.setState({ articles })
    console.log(this.state);

  }

  onFileChange = (e) => {
    this.setState({ "multirefiles": e.target.files })
  }
  onFileChangeschedule = (e) => {
    this.setState({ "multirefiles_tab3": e.target.files })
  }
  onFileChangescheduleimage = (e) => {
    this.setState({ "multirefilesimages_tab3": e.target.files })
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value

    });
  }
  handleChangeArray = async (e) => {
    // (this.state.arrayinsert).push(e.target.value);
    // this.setState({
    //   [e.target.id]: this.state.arrayinsert

    // });

    const item = e.target.name;
    const isChecked = e.target.checked;
    await this.setState(prevState => ({ checkedItems: prevState.checkedItems.set(item, isChecked) }));




  }






  handleSubmit = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append('title', this.state.title);
    form_data.append('textboxdata', this.state.textboxdata);
    if (!this.state.titile & !this.state.textboxdata) {
      return this.setState(state => ({

        alertcolor: 'danger',
        isalert: true,
        alertmsg: 'Something Went Wrong Check Form Fields'

      }));


    }
    let url = 'http://localhost:8099/telegram/add/';

    const config = {
      headers: { 'content-type': 'multipart/form-data' }
    }
    this.setState(state => ({
      isProcessing: !state.isProcessing
    }));
    axios.post(url, form_data, config)
      .then(res => {
        console.log(res.data);
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'success',
          isalert: true,
          alertmsg: 'Great Job! Created Successfully'

        }));
      })
      .catch(err => {
        alert(err)
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'danger',
          isalert: true,
          alertmsg: 'Something Went Wrong Check Form Fields'
        }));

      })
  };



  handleSendMessage = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append('sendnowtitle', this.state.sendnowtitle);
    // form_data.append('groupdatatab2', this.state.groupdatatab2);
    form_data.append('masgtyptab2', this.state.masgtyptab2);





    for (const key of Object.keys(this.state.multirefiles_tab3)) {
      form_data.append('msg', this.state.multirefiles_tab3[key])
    }
    if (this.state.masgtyptab2 !== 'textmessage') {

      // form_data.append('msg', this.state.multirefiles_tab3);
      for (const key of Object.keys(this.state.multirefilesimages_tab3)) {
        form_data.append('msgimages', this.state.multirefilesimages_tab3[key])
      }

    }

    if (this.state.masgtyptab2 === 'textmessage') {
      form_data.append('msg', this.state.textboxdata);
    }
    else {
      form_data.append('msg', this.state.multirefiles);
    }

    if (this.state.sendnowtitle === 'group') {
      form_data.append('groupdatatab2', this.state.gpdata);
    }
    else {
      form_data.append('groupdatatab2', this.state.chdata);
    }


    let url = 'http://localhost:8099/telegram/post/';

    const config = {
      headers: { 'content-type': 'multipart/form-data' }
    }
    this.setState(state => ({
      isProcessing: !state.isProcessing
    }));
    axios.post(url, form_data, config)
      .then(res => {
        console.log(res.data);
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'success',
          isalert: true,
          alertmsg: 'Great Job! Send Successfully'

        }));
      })
      .catch(err => {
        alert(err)
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'danger',
          isalert: true,
          alertmsg: 'Something Went Wrong Check Form Fields'
        }));

      })
  };



  handleSchedule = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append('scheduletitle', this.state.scheduletitle);
    form_data.append('masgtyptab2', this.state.masgtyptab2);
    // form_data.append('timenow', this.state.timenow);
    // form_data.append('endtime', this.state.endtime);
    form_data.append('gap', this.state.gap);
    form_data.append('maxtime', this.state.maxtime);

    var allchecks = [];
    allchecks = document.getElementsByName('childcheckbox');
    let truebox = []
    Object.entries(allchecks).filter(([key, val]) => {
      if (val.checked === true) {
        truebox.push(val.id)

      }

      return truebox
    })


    for (const key of Object.keys(this.state.multirefiles_tab3)) {
      form_data.append('msg', this.state.multirefiles_tab3[key])
    }
    if (this.state.masgtyptab2 !== 'textmessage') {

      // form_data.append('msg', this.state.multirefiles_tab3);
      for (const key of Object.keys(this.state.multirefilesimages_tab3)) {
        form_data.append('msgimages', this.state.multirefilesimages_tab3[key])
      }

    }



    form_data.append('groupdatatab2', truebox);

    let url = 'http://localhost:8099/telegram/schedule/';

    const config = {
      headers: { 'content-type': 'multipart/form-data' }
    }
    this.setState(state => ({
      isProcessing: !state.isProcessing
    }));
    axios.post(url, form_data, config)
      .then(res => {
        console.log(res.data);
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'success',
          isalert: true,
          alertmsg: 'Great Job! Send Successfully'

        }));
      })
      .catch(err => {
        alert(err)
        this.setState(state => ({
          isProcessing: !state.isProcessing,
          alertcolor: 'danger',
          isalert: true,
          alertmsg: 'Something Went Wrong Check Form Fields'
        }));

      })
  };























  getfilename = (e) => {

    this.setState({
      "file_name": e.target.files[0]

    });
  }



  render() {

    const groupitem = this.state.groupdata.map((item, key) =>

      <CustomInput inline
        id={item.val}
        key={item.id}
        type="checkbox"
        name='childcheckbox' checked={this.state.checkedItems.get(item.val)}
        onChange={this.handleChangeArray}>
        {item.val}
      </CustomInput>


    )



    const channelitems = this.state.channeldata.map((item, key) =>

      <CustomInput inline
        id={item.val}
        key={item.id}
        type="checkbox"
        name='childcheckbox' checked={this.state.checkedItems.get(item.val)}
        onChange={this.handleChangeArray}>
        {item.val}
      </CustomInput>


    )

    // const channelitems2 = this.state.channeldata.map((item, key) =>

    //   {item.val}


    // )



    const isProcessing = this.state.isProcessing;
    return (
      <>
        <Header />
        {/* Page content */}

        <Container className="mt--7 mb--3" fluid>


          <Card>
            <CardHeader>
              <p>Telegram Dashboard</p>
            </CardHeader>
            <CardBody>


              <div className="nav-wrapper">


                <Nav
                  className="nav-fill flex-column flex-md-row"
                  id="tabs-icons-text"
                  pills
                  role="tablist"
                >
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 1}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 1
                      })}
                      onClick={e => this.toggleNavs(e, "tabs", 1)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-cloud-upload-96 mr-2" />
                Add
              </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 2}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 2
                      })}
                      onClick={e => this.toggleNavs(e, "tabs", 2)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-bell-55 mr-2" />
                Send Now
              </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 3}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 3
                      })}
                      onClick={e => this.toggleNavs(e, "tabs", 3)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-calendar-grid-58 mr-2" />
                Schedule
              </NavLink>
                  </NavItem>
                </Nav>
              </div>
              <Card className="shadow">
                <CardBody>
                  <TabContent activeTab={"tabs" + this.state.tabs}>
                    <TabPane tabId="tabs1">


                      <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ?
                              <Alert className={`alert-${this.state.alertcolor}`} fade={true}>
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong> {this.state.alertmsg}
                                </span>
                              </Alert>
                              :
                              null
                            }
                            <FormGroup className="mb-3">
                              <Label>I Want to Add :</Label>
                              <InputGroup className="input-group-alternative">

                                <Input type="select" name="title" id="title" value={this.state.title} onChange={this.handleChange}>
                                  <option value="group">Group</option>
                                  <option value="channel">Channel</option>

                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                        </Row>

                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Place Channels or Groups By Enter : </Label>
                              <InputGroup className="input-group-alternative">

                                <Input required type="textarea" name="textboxdata" id="textboxdata"
                                  placeholder="Your Data Categorized by Enter"
                                  rows="10"
                                  value={this.state.textboxdata} onChange={this.handleChange}>


                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>

                        </Row>
                        <div className="text-center">
                          <Button disabled={this.state.isProcessing} onClick={this.handleSubmit} className="my-4" color="primary" type="button">
                            Submit
                  </Button>

                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                      </Form>












                    </TabPane>
                    <TabPane tabId="tabs2">



                      <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ?
                              <Alert className={`alert-${this.state.alertcolor}`} fade={true}>
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong> {this.state.alertmsg}
                                </span>
                              </Alert>
                              :
                              null
                            }
                            <FormGroup className="mb-3">
                              <Label>I Want to Send Through:</Label>
                              <InputGroup className="input-group-alternative">

                                <Input type="select" name="sendnowtitle" id="sendnowtitle" value={this.state.sendnowtitle} onChange={this.handleChange}>
                                  <option value="group">Group</option>
                                  <option value="channel">Channel</option>

                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                        </Row>





                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select <strong>{this.state.sendnowtitle}</strong></Label>
                              <InputGroup className="">
                                {this.state.sendnowtitle === "group" ?
                                  <Input id="gpdata" onChange={this.handleChange} type="textarea" value={this.state.gpdata}>

                                  </Input>
                                  :
                                  <Input id="chdata" onChange={this.handleChange} type="textarea" value={this.state.chdata}>

                                  </Input>

                                }



                              </InputGroup>


                            </FormGroup>
                          </Col>

                        </Row>

                        {/* Temporary 
                        Comment */}




                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select Message Type</Label>
                              <InputGroup className="input-group-alternative">

                                <Input type="select" name="masgtyptab2" id="masgtyptab2" value={this.state.masgtyptab2} onChange={this.handleChange}>
                                  <option value=''>Click to Select</option>
                                  <option value="textmessage">Send Message</option>
                                  <option value="photoupload">Send Photo</option>
                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>

                        </Row>


                        <Row>
                          <Col md="12">

                            {this.state.masgtyptab2 === 'textmessage' ?
                              <FormGroup className="mb-3">
                                <Label>Place Message Now : </Label>
                                <InputGroup className="input-group-alternative">

                                  <Input required type="textarea" name="textboxdata" id="textboxdata"
                                    placeholder="Enter a Message"
                                    rows="10"
                                    value={this.state.textboxdata} onChange={this.handleChange}>


                                  </Input>
                                </InputGroup>
                              </FormGroup>

                              :
                              <FormGroup className="mb-3">
                                <Label for="multirefiles">Upload Bg Files</Label>
                                <InputGroup className="input-group-alternative">
                                  <CustomInput type="file" id="multirefiles" name="multirefiles"
                                    onChange={this.onFileChange} label="Yo, pick a files!" />
                                </InputGroup>
                              </FormGroup>
                            }


                          </Col>

                        </Row>






                        <div className="text-center">
                          <Button disabled={this.state.isProcessing} onClick={this.handleSendMessage} className="my-4" color="primary" type="button">
                            Send Now
                  </Button>

                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                      </Form>








                    </TabPane>
                    <TabPane tabId="tabs3">


                      <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ?
                              <Alert className={`alert-${this.state.alertcolor}`} fade={true}>
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong> {this.state.alertmsg}
                                </span>
                              </Alert>
                              :
                              null
                            }
                            <FormGroup className="mb-3">
                              <Label>I Want to Scedule :</Label>
                              <InputGroup className="input-group-alternative">

                                <Input type="select" name="scheduletitle" id="scheduletitle" value={this.state.scheduletitle} onChange={this.handleChange}>
                                  <option value="group">Group</option>
                                  <option value="channel">Channel</option>

                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                        </Row>


                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select <strong>{this.state.scheduletitle}</strong></Label>
                              <InputGroup className="">

                                Channels: {channelitems}

                              </InputGroup>
                              <hr></hr>
                              <InputGroup className="">
                                Groups: {groupitem}
                              </InputGroup>
                            </FormGroup>
                          </Col>

                        </Row>
                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select Message Type</Label>
                              <InputGroup className="input-group-alternative">

                                <Input type="select" name="masgtyptab2" id="masgtyptab2" value={this.state.masgtyptab2} onChange={this.handleChange}>
                                  <option value=''>Click to Select</option>
                                  <option value="textmessage">Send Message</option>
                                  <option value="photoupload">Send Photo</option>
                                  <option value="videoupload">Send Video</option>
                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>

                        </Row>

                        <Row>
                          {/* <Col md="4">

                            <FormGroup className="mb-3">
                              <Label>From :</Label>
                              <InputGroup className="input-group-alternative">
                              <Input type="number" id="timenow" name="timenow" onChange={this.handleChange} value={this.state.timenow} />
                               
                              </InputGroup>
                            </FormGroup>
                          </Col>
                          <Col md="4">
                          <FormGroup className="mb-3">
                              <Label>End :</Label>
                              <InputGroup className="input-group-alternative">
                              <Input type="number" id ="endtime" name="endtime" onChange={this.handleChange} value={this.state.endtime} />
                               
                              </InputGroup>
                            </FormGroup>
                          </Col> */}

                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Gap (Seconds): </Label>
                              <InputGroup className="input-group-alternative">
                                <Input id="gap" name="gap" type="number" onChange={this.handleChange} value={this.state.gap} />

                              </InputGroup>
                            </FormGroup>
                          </Col>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Maximum Hours to Repeat the task (0-23 HRS) :</Label>
                              <InputGroup className="input-group-alternative">
                                <Input id="maxtime" name="maxtime" type="number" onChange={this.handleChange} value={this.state.maxtime} />

                              </InputGroup>
                            </FormGroup>
                          </Col>
                        </Row>

                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label for="multirefiles_tab3">Upload Excel</Label>
                              <InputGroup className="input-group-alternative">
                                <CustomInput type="file" id="multirefiles_tab3" name="multirefiles_tab3" accept='.xlsx'
                                  onChange={this.onFileChangeschedule} label="Yo, pick a files!" />
                              </InputGroup>
                            </FormGroup>
                          </Col>

                        </Row>


                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label for="multirefilesimages_tab3">Upload Images</Label>
                              <InputGroup className="input-group-alternative">
                                <CustomInput type="file" id="multirefilesimages_tab3" multiple name="multirefilesimages_tab3" accept='.png'
                                  onChange={this.onFileChangescheduleimage} label="Yo, pick a files!" />
                              </InputGroup>
                            </FormGroup>
                          </Col>

                        </Row>


                        <div className="text-center">
                          <Button disabled={this.state.isProcessing} onClick={this.handleSchedule} className="my-4" color="primary" type="button">
                            Schedule
                  </Button>

                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                      </Form>



                    </TabPane>
                  </TabContent>
                </CardBody>
              </Card>
            </CardBody>
          </Card>
        </Container>

      </>
    );
  }
}


const mapStateToProps = (state, ownProps) => {
  return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
    username: state.login.username
  };
}

export default connect(mapStateToProps)(TelegramApi);