
import React from "react";
import axios from "axios";
import { connect } from "react-redux";
// import { Link } from "react-router-dom";
// reactstrap components

import {
    Card, Container, Button, Row, 

    Badge,
    CardFooter,
    CardHeader,
    // CardBody,
    // Label,
    Pagination,
    PaginationItem,
    PaginationLink,
    Table,
    Input,
    Col,

    Spinner
} from "reactstrap";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

import StyledDropzone from "views/examples/subcomponents/uploadform.js"
// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}

class CreatedFolders extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            title: 0,
            author: this.props.username,
            articles: [],
            checkedItems: new Map(),
            checkedvalues : [],
            isProcessing:false,
            numiterations:6,
            deleteitems : []

        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleDownload = this.handleDownload.bind(this);
        this.onTodoChange = this.onTodoChange.bind(this);
    }
    
    
    fetchArticles = () => {
        
        axios.get(`http://localhost:8099/home/${this.state.author}/detailed/folders`).then(res => {

            this.setState({
                articles: res.data.response
            });
        });

    }

    handleSubmit(e) {
        e.preventDefault();
        var form_data = this.state.checkedvalues;
        this.setState(state => ({
            isProcessing: !state.isProcessing
          }));
        const config = {
            headers: { 'Content-Type': 'application/json' }
        }
        axios.post(`http://localhost:8099/video/${this.state.author}/${this.state.numiterations}`,form_data,config).then(res => {
            console.log(form_data);
            this.setState(state => ({
                isProcessing: !state.isProcessing
              }));
        });

    }

    handleDownload = (e) => {
        e.preventDefault();
        var form_data = this.state.checkedvalues;
        this.setState(state => ({
            isProcessing: !state.isProcessing
          }));
        const config = {
            headers: { 'Content-Type': 'application/json' },
            responseType: 'blob'
        }
        axios.post(`http://localhost:8099/download/${this.state.author}/${e.target.id}`,form_data,config).then(res => {
            console.log(res);
            
            this.setState(state => ({
                isProcessing: !state.isProcessing
              }));
              const url = window.URL.createObjectURL(new Blob([res.data]));
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', 'file.zip'); //or any other extension
                document.body.appendChild(link);
                link.click();

            
        });

    }




    

    componentDidMount() {
        this.fetchArticles();
    }



    handleDelete = (item) => {
        var form_data = this.state.deleteitems;
        const articles = this.state.articles.filter(i => i.id !== item.id);
        this.setState({ articles });
        console.log(this.state);
        form_data.push(item.id)
        
        const config = {
            headers: { 'Content-Type': 'application/json' }
        }
        axios.delete(`http://localhost:8099/deleter/${this.state.author}/${item.id}`,form_data,config).then(res => {
            alert('deleted')
        });

    }

    handleChange(e) {
        let checkedvalues = this.state.checkedvalues
        const item = e.target.name;
        const isChecked = e.target.checked;
        this.setState(prevState => ({ checkedItems: prevState.checkedItems.set(item, isChecked) }));
        checkedvalues.push(item)
        this.setState({checkedvalues: checkedvalues})             
        
      } 

    onTodoChange = (e) =>{
        this.setState(state => ({
            'numiterations': e
          }));
        }

    render() {

        const items = this.state.articles.map((item, key) =>
            <tr key={item.source}>
                {/* <td> */}
{/*                     
                    <input className="custom-control-input" type="checkbox" name={item.source} checked={this.state.checkedItems.get(item.name)} 
                onChange={this.handleChange} /> */}
                



                <td>
                    <div className="custom-control custom-control-alternative custom-checkbox">
                    <input
                    className="custom-control-input"
                    id={item.source}
                    type="checkbox"
                    name={item.source} checked={this.state.checkedItems.get(item.name)} 
                onChange={this.handleChange}
                  />
                  <label
                    className="custom-control-label"
                    htmlFor={item.source}
                    style={{marginBottom:"12px"}}
                  >
                    
                  </label>
                    </div>
                  
                </td>
                        
                        {/* </td> */}
                
                
                        











 




                <td>
                    
                    {item.source}  <Link to={`/admin/created-images/${item.source}`}><i className=" ni ni-image"></i></Link> |  <Link to={`/admin/created-videos/${item.source}`}><i className=" ni ni-tv-2"></i></Link>


                </td>
                <td>
                    <Badge color="primary" className="badge-dot mr-4">
                        <i className="bg-warning" />
                        {item.author}
                    </Badge>
                </td>
                <td>

                    <Button id={item.source} onClick={this.handleDelete.bind(this, item)} className="my-2" color="danger" type="button">
                        Delete
                    </Button>
                   
                </td>


            </tr>
        )

        return (
            <>
                <Header />
                {/* Page content */}

                <Container className="mt--7" fluid>

                    <Row>
                        

                    
                            <Col>
                            <Card className="shadow">
                                <CardHeader className="border-0">
                                    
                                    <h3 className="mb-0"><label>Select num of Images for each Video: </label><Input value ={this.state.numiterations} onChange={(e) => this.onTodoChange(e.target.value)} type="number" /></h3>                                    
                                    <Button disabled={this.state.isProcessing} onClick={this.handleSubmit} className="my-4" color="success" type="button">
                                        Create Video
                                    </Button>
                                    <Button disabled={this.state.isProcessing} onClick={this.handleDownload} id="mp4" className="my-4" color="info" type="button">
                                        Download Videos
                                    </Button>
                                    <Button disabled={this.state.isProcessing} onClick={this.handleDownload} id="png" className="my-4" color="warning" type="button">
                                        Download Images
                                    </Button>
                                    {this.state.isProcessing ? <Spinner color="primary" /> : null}
                                    
                                </CardHeader>
                                <Table className="align-items-center table-flush" responsive>
                                    <thead className="thead-light">
                                        <tr>
                                            <th scope="col">Box</th>
                                            <th scope="col">Source</th>
                                            <th scope="col">Author</th>
                                            <th scope="col">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {items}
                                    </tbody>
                                </Table>
                                <CardFooter className="py-4">
                                    <nav aria-label="...">
                                        <Pagination
                                            className="pagination justify-content-end mb-0"
                                            listClassName="justify-content-end mb-0"
                                        >
                                            <PaginationItem className="disabled">
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                    tabIndex="-1"
                                                >
                                                    <i className="fas fa-angle-left" />
                                                    <span className="sr-only">Previous</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem className="active">
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    1
                        </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    2 <span className="sr-only">(current)</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    3
                        </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    <i className="fas fa-angle-right" />
                                                    <span className="sr-only">Next</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                        </Pagination>
                                    </nav>
                                </CardFooter>
                            </Card>
                            </Col>
                        
                    </Row>
                </Container>

            </>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
      token: state.login.token,
      status: state.login.status,
      username: state.login.username
    };
  }


export default connect(mapStateToProps)(CreatedFolders);