
import React from "react";
import axios from "axios";
import { Link } from "react-router-dom";
// reactstrap components
import DjangoCSRFToken from 'django-react-csrftoken'
import {
    Card, Container, Button, Row, FormGroup,

    Badge,
    CardFooter,
    CardHeader,
    CardBody,
    Label,
    Pagination,
    PaginationItem,
    PaginationLink,
    Table,
    Spinner,
    CustomInput
} from "reactstrap";

import StyledDropzone from "views/examples/subcomponents/uploadform.js"
// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}
import { connect } from "react-redux";
import ReactPlayer from 'react-player'
const OPTIONS = [];
class CreatedImages extends React.Component {
    
    
    constructor(props) {
        super(props);
        
        
        this.state = {
            title: 0,
            author: this.props.username,
            foldername: '',
            articles: [],
            checkedvalues : [],
            checkedItems: new Map(),
            isProcessing:false,
            checked:false
        };
        this.downloadFile = this.downloadFile.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.bulkprocessor = this.bulkprocessor.bind(this);
        // this.selectAllCheckboxes = this.selectAllCheckboxes(this);
    }

    


    fetchArticles = (foldername) => {
        axios.get(`http://localhost:8099/home/${this.state.author}/${foldername}/images`).then(res => {

            this.setState({
                articles: res.data.response,
                foldername: foldername
            });
        });
        

    }

    handleSubmit(e) {
        e.preventDefault();
        
        this.setState(state => ({
            isProcessing: !state.isProcessing
          }));


          let truebox = []
                
          // for (let [key,value] of Object.entries(allchecks)) {
          //     console.log(key,value.);
          // }
        let allchecks = document.getElementsByName('childcheckbox');
        Object.entries(allchecks).filter(([key,val]) => {
        if (val.checked === true){
            truebox.push(val.id)
        }
        return truebox
        })
        const config = {
            headers: { 'Content-Type': 'application/json' },
            responseType: 'blob'
        }
        axios.post(`http://localhost:8099/pdf/${this.state.author}/${this.state.foldername}`,truebox,config).then(res => {
            console.log(truebox);
            this.setState(state => ({
                isProcessing: !state.isProcessing
              }));
              const url = window.URL.createObjectURL(new Blob([res.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', 'file.pdf'); //or any other extension
            document.body.appendChild(link);
            link.click();
        });

    }


    componentDidMount() {
        const foldername = this.props.match.params.foldername       
        
        this.fetchArticles(foldername);
    }

    // selectAllCheckboxes = isSelected => {
    //     Object.keys(this.state.checkboxes).forEach(checkbox => {
    //       // BONUS: Can you explain why we pass updater function to setState instead of an object?
    //       this.setState(prevState => ({
    //         checkboxes: {
    //           ...prevState.checkboxes,
    //           [checkbox]: isSelected
    //         }
    //       }));
    //     });
    //   };

    handleDelete = (item) => {
                         
        
        const articles = this.state.articles.filter(i => i.id !== item.id);
        this.setState({ articles });
        
        const config = {
            headers: { 'Content-Type': 'application/json' }
        }
        axios.post(`http://localhost:8099/home/${this.state.author}/${this.state.foldername}/deletes`,item.id,config).then(res => {
            alert(res.data);
        });

    }


    downloadFile = (e) => {
        e.preventDefault();
        
        this.setState(state => ({
            isProcessing: !state.isProcessing
          }));
        const config = {
            headers: { 'Content-Type': 'application/json' },
            responseType: 'blob'
        }
        
        axios.post(`http://localhost:8099/imagedownload/${this.state.author}/${this.state.foldername}/pic1`,config).then(res => {
            console.log(res);
            
            this.setState(state => ({
                isProcessing: !state.isProcessing
              }));

              
              
              const url = window.URL.createObjectURL(new Blob([res.data],{type: 'image/png'}));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'file.png'); //or any other extension
              document.body.appendChild(link);
              link.click();

            
        });
    }   



    handleChange = async (e) => {
        // let checkedvalues = this.state.checkedvalues
        // let checkedvalues = this.state.checkedvalues.filter(i => i.id !== item.id)
        // const item = e.target.name;     
                
        // const item = e.target.name;
        // const isChecked = e.target.checked;
        // let values = this.state.checkedvalues.push(e.target.name);
        
        // this.setState({ checked: e.target.id.checked })
        // var allchecks = document.getElementsByName('childcheckbox');
        // for (let [key, value] of Object.entries(allchecks)) {
        //     console.log(key, value.checked);
        // }


        // var filtervals = allchecks.filter(val => {
        //     console.log(val===true);
        //     return val;

        // })
        
        const item = e.target.name;
        const isChecked = e.target.checked;
        await this.setState(prevState => ({ checkedItems: prevState.checkedItems.set(item, isChecked) }));
        
        // for (let [key,value] of (this.state.checkedItems)) {
        //     console.log(key,value);
        // }
        // let newval = [...this.state.checkedItems].map((key,val)=>{
            
        //     return key
        // })
        // console.log(newval);
        
       
    

       

        }
        
        
        // this.setState(prevState => ({ checkedItems: prevState.checkedItems.set(item, isChecked) }));
        
                    
        bulkprocessor=async(e)=>{
            var allchecks = [];
            let i;
            allchecks = document.getElementsByName('childcheckbox');

            if (e.target.id === 'selectall'){
                if (e.target.checked===true){
                    
                    // document.getElementById('idhere').checked = false;
                     for (i of allchecks){
                         console.log(i.id);
                         document.getElementById(i.id).checked = true;

                     }

                }
                else{

                    for (i of allchecks){
                        console.log(i.id);
                        document.getElementById(i.id).checked = false;

                    }

                }
                // console.log(e.target.checked);

            }
            else{
                let truebox = []
                
                
                // for (let [key,value] of Object.entries(allchecks)) {
                //     console.log(key,value.);
                // }
                Object.entries(allchecks).filter(([key,val]) => {
                if (val.checked === true){
                    truebox.push(val.id)
                    let articles = this.state.articles.filter(i => i.id !== val.id);
                    this.setState({ articles });
                }
                
                return truebox
                })
                console.log(truebox);

                
                var form_data = truebox;

                const config = {
                    headers: { 'Content-Type': 'application/json' }
                }
                axios.post(`http://localhost:8099/bulk/${this.state.author}/${this.state.foldername}/images`,form_data,config).then(res => {
                    alert(res.data);
                });
                
                

            }
            
            

        }
        
      



    render() {

        const items = this.state.articles.map((item, key) =>
            <tr key={item.source}>
                <td><CustomInput inline
                    id={item.source}
                    type="checkbox"
                    name='childcheckbox' checked={this.state.checkedItems.get(item.name)}
                onChange={this.handleChange}>


                    </CustomInput></td>
                
                <td>
                    {(item.source).toString().includes('.mp4') ?
                    <ReactPlayer controls url={require("assets/img/"+this.state.author+"/"+this.state.foldername+"/"+item.source)} playing />
                    :
                       
                        // <a  href={`http://localhost:8099/static/${this.state.author}/${this.state.foldername}/${item.source}`} download target="_blank">

                        <img alt="..."
                        onClick={this.downloadFile}
                        style = {{width: "50%"}}  
                        id={item.source}                      
                        //src={require("assets/img/"+this.state.author+"/"+this.state.foldername+"/"+item.source)}
                        src = {`http://localhost:8099/static/${this.state.author}/${this.state.foldername}/${item.source}`}
                        />


                        // </a>
                        
                        
                        

                    }
                   
                 </td>
                <td>
                    <Badge color="" className="badge-dot mr-4">
                        <i className="bg-warning" />
                        {item.author}
                    </Badge>
                </td>
                <td>

                    <Button id={item.source} onClick={this.handleDelete.bind(this, item)} className="my-2" color="danger" type="button">
                        Delete
                    </Button>
                </td>


            </tr>
        )

        return (
            <>
                <Header />
                {/* Page content */}

                <Container className="mt--7" fluid>

                    <Row>


                        <div className="col">
                            <Card className="shadow">
                                <CardHeader className="border-0">
                                    <h3 className="mb-0">Card tables</h3>
                                    <Button disabled={this.state.isProcessing} onClick={this.handleSubmit} className="my-4" color="success" type="button">
                                        Create Pdf
                                    </Button>
                                    <Button disabled={this.state.isProcessing} onClick={this.bulkprocessor} className="my-4" id="bulkdelete" color="danger" type="button">
                                        Delete
                                    </Button>
                                </CardHeader>
                                <Table className="align-items-center table-flush" responsive>
                                    <thead className="thead-light">
                                        <tr>
                                            <th scope="col">
                                                <CustomInput type="checkbox" name="parentcheckbox" id="selectall" onChange={this.bulkprocessor} inline />
                                                
                                            </th>                                            
                                            <th scope="col">Source</th>
                                            <th scope="col">Author</th>
                                            <th scope="col">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {items}
                                    </tbody>
                                </Table>
                                <CardFooter className="py-4">
                                    <nav aria-label="...">
                                        <Pagination
                                            className="pagination justify-content-end mb-0"
                                            listClassName="justify-content-end mb-0"
                                        >
                                            <PaginationItem className="disabled">
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                    tabIndex="-1"
                                                >
                                                    <i className="fas fa-angle-left" />
                                                    <span className="sr-only">Previous</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem className="active">
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    1
                        </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    2 <span className="sr-only">(current)</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    3
                        </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    <i className="fas fa-angle-right" />
                                                    <span className="sr-only">Next</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                        </Pagination>
                                    </nav>
                                </CardFooter>
                            </Card>
                        </div>
                    </Row>
                </Container>

            </>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
      username: state.login.username
    };
  }


export default connect(mapStateToProps)(CreatedImages);