import React from "react";
import axios from "axios";
import { connect } from "react-redux";
import classnames from "classnames";

// reactstrap components
import {
  Card,
  Container,
  Button,
  Row,
  FormGroup,
  Form,
  Input,
  InputGroup,
  CardHeader,
  CardBody,
  Label,
  CustomInput,
  Alert,
  Col,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  Spinner,
  Table,
} from "reactstrap";

// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}

class wordpressImagemaker extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "group",
      author: this.props.username,
      tabs: 1,
      groupdata: [],
      isProcessing: false,
      alertcolor: null,
      isalert: false,
      alertmsg: "",
      textboxdata: "",
      images: [],
      checkedItems: new Map(),
      posttitle: '',
      selecttype: '',
      selecttype2: '',
      orderby: 'relevance',
      ytubekwd: '',
      maxresults: 0


    };

  }

  fetchArticles = async () => {
    await axios.get("http://localhost:8099/getimages").then(res => {

      this.setState({
        images: res.data.response,

      });
    });
    console.log(this.state.groupdata);
  }

  toggleNavs = (e, state, index) => {
    e.preventDefault();
    this.setState({
      [state]: index,
    });
  };

  componentDidMount() {
    this.fetchArticles();
    const token = localStorage.getItem("token");
    console.log(token);
    if (!token) {
      this.props.history.push("login");
      // this.props.history.push('register');
    } else {
      this.props.history.push("wordpress-thumbs");

      //  console.log(this.props)
    }
  }



  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append("textboxdata", this.state.textboxdata);
    form_data.append("selecttype", this.state.selecttype2);
    if (!this.state.titile & !this.state.textboxdata) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }

    let url = "http://localhost:8099/screencreator";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };

    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));

    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.fetchArticles();
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };



  handleYtube = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append("orderby", this.state.orderby);
    form_data.append("ytubekwd", this.state.ytubekwd);
    form_data.append("maxresults", this.state.maxresults);
    if (!this.state.orderby & !this.state.ytubekwd) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }

    let url = "http://localhost:8099/wordpresscrud/ytubefetch";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };

    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));

    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.fetchArticles();
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };
  bulkprocessor = async (e) => {
    var allchecks = [];
    let i;
    allchecks = document.getElementsByName('allchildren');
    if (e.target.checked === true) {

      // document.getElementById('idhere').checked = false;
      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = true;

      }

    }
    else {

      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = false;

      }

    }
  }

  toSubmitLive = async (e) => {

    var allchecks = [];
    var truebox = []
    let i;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (i of allchecks) {
      if (i.checked === true) {
        truebox.push(i.id);
      }

    }


    let form_data = new FormData();

    form_data.append("truebox", truebox);
    form_data.append("posttitle", this.state.posttitle);
    form_data.append("selecttype", this.state.selecttype);


    let url = "http://localhost:8099/wordpresscrud/create";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };

    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));

    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });






  }

  toRemoveLive = async (e) => {
    var allchecks = [];
    var truebox = []
    let i;
    let m;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (m of allchecks) {
      if (m.checked === true) {
        truebox.push(m.id)
        let imagealter = parseInt(m.id)
        const images = this.state.images.filter(i => i.id !== imagealter);
        this.setState({ images });

      }


    }
    console.log(truebox);


    let form_data = new FormData();

    form_data.append("truebox", truebox);

    let url = "http://localhost:8099/wordpresscrud/imgdelete";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };

    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));

    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });




  }



  render() {
    const isProcessing = this.state.isProcessing;
    const its = this.state.images.map((item, key) => {
      return <tr key={item.id}>
        <td>
          <div className="custom-control custom-control-alternative custom-checkbox">
            <input
              className="custom-control-input"
              type="checkbox"
              id={item.id}
              name='allchildren' checked={this.state.checkedItems.get(item.id)}
              onChange={this.state.handleChange}
            />
            <label
              className="custom-control-label"
              htmlFor={item.id}
              style={{ marginBottom: "12px" }}
            >

            </label>
          </div>

        </td>
        <td></td>
        <td></td>
        {item.type === 'video' ?
        <td><img style={{ width: '90%' }} alt=".." src={`${item.imagename}`} /></td>
        :
        <td><img style={{ width: '90%' }} alt=".." src={`http://localhost:8099/${item.imagename}`} /></td>
        }
        <td>{item.url}</td>

      </tr>

    })
    return (
      <>
        <Header />
        {/* Page content */}

        <Container className="mt--7 mb--3" fluid>
          <Card>
            <CardHeader>
              <p>Wordpress YouTube Dashboard</p>
            </CardHeader>
            <CardBody>
              <div className="nav-wrapper">
                <Nav
                  className="nav-fill flex-column flex-md-row"
                  id="tabs-icons-text"
                  pills
                  role="tablist"
                >
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 1}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 1,
                      })}
                      onClick={(e) => this.toggleNavs(e, "tabs", 1)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-cloud-upload-96 mr-2" />
                      Add
                    </NavLink>
                  </NavItem>

                </Nav>
              </div>
              <Card className="shadow">
                <CardBody>
                  <TabContent activeTab={"tabs" + this.state.tabs}>
                    <TabPane tabId="tabs1">
                      <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ? (
                              <Alert
                                className={`alert-${this.state.alertcolor}`}
                                fade={true}
                              >
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong>{" "}
                                  {this.state.alertmsg}
                                </span>
                              </Alert>
                            ) : null}
                          </Col>
                        </Row>

                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Place Urls By Enter : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="textarea"
                                  name="textboxdata"
                                  id="textboxdata"
                                  placeholder="Your Data Categorized by Enter"
                                  rows="10"
                                  value={this.state.textboxdata}
                                  onChange={this.handleChange}
                                ></Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select Type : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="select"
                                  name="selecttype2"
                                  id="selecttype2"
                                  value={this.state.selecttype2}
                                  onChange={this.handleChange}
                                >
                                  <option>Select Type</option>
                                  <option value="video">Video</option>
                                  <option value="channel">Channel</option>
                                  <option value="other">Other</option>


                                </Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select Order : (For YouTube Only) </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="select"
                                  name="orderby"
                                  id="orderby"
                                  value={this.state.orderby}
                                  onChange={this.handleChange}
                                >
                                  <option>Select Type</option>
                                  <option value="date">date</option>
                                  <option value="title">Title</option>
                                  <option value="rating">Rating</option>
                                  <option value="relevance">Relevance</option>
                                  <option value="videoCount">VideoCount</option>
                                  <option value="viewCount">viewCount</option>


                                </Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Enter Keyword : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="text"
                                  name="ytubekwd"
                                  id="ytubekwd"
                                  placeholder="Enter Keyword"
                                  value={this.state.ytubekwd}
                                  onChange={this.handleChange}
                                ></Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>

                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Maximum Results : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="select"
                                  name="maxresults"
                                  id="maxresults"
                                  value={this.state.maxresults}
                                  onChange={this.handleChange}
                                >
                                  <option>Select Type</option>
                                  <option value="0">50</option>
                                  <option value="1">100</option>
                                  <option value="2">150</option>
                                  <option value="3">200</option>
                                  


                                </Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>
                        </Row>
                        <div className="text-center">
                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleSubmit}
                            className="my-4"
                            color="primary"
                            type="button"
                          >
                            Submit
                          </Button>
                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleYtube}
                            className="my-4"
                            color="primary"
                            type="button"
                          >
                            YouTube Search
                          </Button>

                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                        
                      </Form>
                    </TabPane>
                  </TabContent>
                  <Row>
                    <Col>
                      {/* <Card className="shadow border-0">
                <CardBody> */}
                      <Row>
                        <Col md="12">
                          <FormGroup className="mb-3">

                            <InputGroup className="input-group-alternative">
                              <Input
                                required
                                type="text"
                                name="posttitle"
                                id="posttitle"
                                placeholder="Enter Title Post"
                                value={this.state.posttitle}
                                onChange={this.handleChange}
                              ></Input>
                            </InputGroup>
                          </FormGroup>

                        </Col>
                        <Col md="12">
                          <FormGroup className="mb-3">
                            <Label>Select Type : </Label>
                            <InputGroup className="input-group-alternative">
                              <Input
                                required
                                type="select"
                                name="selecttype"
                                id="selecttype"
                                value={this.state.selecttype}
                                onChange={this.handleChange}
                              >
                                <option>Select Type</option>
                                <option value="video">Video</option>
                                <option value="channel">Channel</option>
                                <option value="other">Other</option>


                              </Input>
                            </InputGroup>
                          </FormGroup>

                        </Col>
                      </Row>
                      <h3>{this.state.imagesendmsg}</h3>
                      <Table className="align-items-center table-flush" responsive>

                        <thead className="thead-light">

                          <tr>
                            <th scope="col">
                              <CustomInput type="checkbox" name="parentcheckbox" id="selectall" onChange={this.bulkprocessor} inline />
                            </th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col">Source</th>
                            <th scope="col">
                              <Button
                                className="my-2"
                                color="primary"
                                type="button"
                                onClick={this.toSubmitLive}
                                disabled={this.state.isProcessing}
                              >
                                Send Images
                          </Button>
                              {isProcessing ? <Spinner color="primary" /> : null}
                            </th>
                            <th scope="col">
                              <Button disabled={this.state.isProcessing} className="my-2" color="danger" type="button" onClick={this.toRemoveLive}>
                                Remove Images
                          </Button>
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {its}
                        </tbody>
                      </Table>
                      {/* </CardBody>
              </Card> */}
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </CardBody>
          </Card>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
    username: state.login.username,
  };
};

export default connect(mapStateToProps)(wordpressImagemaker);
