import React from "react";
import axios from "axios";
import { connect } from "react-redux";
import classnames from "classnames";

// reactstrap components
import {
  Card,
  CardFooter,
  Container,
  Button,
  Row,
  FormGroup,
  Form,
  Input,
  InputGroup,
  CardHeader,
  CardBody,
  Label,
  CustomInput,
  Alert,
  Col,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  Spinner,
  Table,
  Pagination,
  PaginationItem,
  PaginationLink
} from "reactstrap";

// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}

class wordpressGet extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "group",
      author: this.props.username,
      tabs: 1,
      images: [],
      isProcessing: false,
      alertcolor: null,
      isalert: false,
      alertmsg: "",
      sitename: "",
      selecttype2: "",
      checkedItems: new Map(),
      titledata: '',
      textboxdata: '',
      currentpage: 0,
      searchin:'',
      searchword:'',
      operation:'',
      updatebox:'',
      docxbunch:''
    };

  }

  fetchArticles = async () => {
    await axios.post("http://localhost:8099/wordpresscrud/retrieveposts").then(res => {

      this.setState({
        images: res.data.response,

      });
    });
    console.log(this.state.groupdata);
  }

  toggleNavs = (e, state, index) => {
    e.preventDefault();
    this.setState({
      [state]: index,
    });
  };

  componentDidMount() {
    // this.fetchArticles();
    const token = localStorage.getItem("token");
    console.log(token);
    if (!token) {
      this.props.history.push("login");
      // this.props.history.push('register');
    } else {
      this.props.history.push("wordpress-get");

      //  console.log(this.props)
    }
  }

  onStaticNewFileChange = (e) => {
    this.setState({ "docxbunch": e.target.files })
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append("sitename", this.state.sitename);
    form_data.append("selecttype", this.state.selecttype2);
    if (!this.state.selecttype2 & !this.state.sitename) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }
    let url = "http://localhost:8099/wordpresscrud/getnfetch";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };
    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));
    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };

  
  handleDocx = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append("sitename", this.state.sitename);
    form_data.append("selecttype", this.state.selecttype2);
    for (const key of Object.keys(this.state.docxbunch)) {
      form_data.append('docxbunch', this.state.docxbunch[key])
    }
    if (!this.state.selecttype2 & !this.state.sitename) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }
    let url = "http://localhost:8099/wordpresscrud/docxparser";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };
    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));
    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };

  handleFilteredTable = (e) => {
    

    let form_data = new FormData();
 

    form_data.append("sitename", this.state.sitename);
    form_data.append("selecttype", this.state.selecttype2);
    form_data.append("searchin", this.state.searchin);
    form_data.append("searchword", this.state.searchword);
    
    if (!this.state.selecttype2 & !this.state.sitename) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }

    let url = "http://localhost:8099/wordpresscrud/filterphase";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };

    if (e.target.id === "backiterated"){
      this.setState((state) => ({
        isProcessing: !state.isProcessing,
        currentpage: state.currentpage - 1
      }));

    }
    else{
      this.setState((state) => ({
        isProcessing: !state.isProcessing,
        currentpage: state.currentpage + 1
      }));

    }
    

    form_data.append("currentpage", this.state.currentpage);
    
    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
          images: res.data.response
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };

  handleSubmitTable = (e) => {
    e.preventDefault();

    let form_data = new FormData();
    if (e.target.id === "resetclass"){
      this.setState(() => ({
        currentpage: 0
      }));

    };    

    form_data.append("sitename", this.state.sitename);
    form_data.append("selecttype", this.state.selecttype2);
    
    if (!this.state.selecttype2 & !this.state.sitename) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }
    let url = "http://localhost:8099/wordpresscrud/retrieveposts";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };
if (e.target.id === "backiterated"){
  this.setState((state) => ({
    isProcessing: !state.isProcessing,
    currentpage: state.currentpage - 1
  }));

}
else{
  this.setState((state) => ({
    isProcessing: !state.isProcessing,
    currentpage: state.currentpage + 1
  }));

}
    

    form_data.append("currentpage", this.state.currentpage);
    
    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
          images: res.data.response
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };

  toupdate = (e) => {
    e.preventDefault();
    var allchecks = [];
    var truebox = []
    let i;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (i of allchecks) {
      if (i.checked === true) {
        truebox.push(i.id);
      }

    }

    let form_data = new FormData();
    form_data.append("truebox", truebox);
    form_data.append("sitename", this.state.sitename);
    form_data.append("selecttype", this.state.selecttype2);
    form_data.append("titledata", this.state.titledata);
    form_data.append("textboxdata", this.state.textboxdata);
    form_data.append("operation", this.state.operation);

    let url = "http://localhost:8099/wordpresscrud/updatepost";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };
    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));
    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully"

        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };

  bulkprocessor = async (e) => {
    var allchecks = [];
    let i;
    allchecks = document.getElementsByName('allchildren');
    if (e.target.checked === true) {

      // document.getElementById('idhere').checked = false;
      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = true;

      }

    }
    else {

      for (i of allchecks) {
        console.log(i.id);
        document.getElementById(i.id).checked = false;

      }

    }
  }

  

  addtoQueue = async (e) => {
    var allchecks = [];
    var truebox = []    
    let m;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (m of allchecks) {
      if (m.checked === true) {
        truebox.push(m.id);       
        
        this.setState(() => ({
          updatebox: truebox,
        }));
        

      }


    }
    console.log(truebox);


   



  }

  toRemoveLive = async (e) => {
    var allchecks = [];
    var truebox = []
    let i;
    let m;
    allchecks = document.getElementsByName('allchildren');


    // document.getElementById('idhere').checked = false;
    for (m of allchecks) {
      if (m.checked === true) {
        truebox.push(m.id)
        let imagealter = parseInt(m.id)
        const images = this.state.images.filter(i => i.id !== imagealter);
        this.setState({ images });

      }


    }
    console.log(truebox);


    let form_data = new FormData();

    form_data.append("truebox", truebox);   
    form_data.append("sitename", this.state.sitename);
    form_data.append("selecttype", this.state.selecttype2); 

      let url = "http://localhost:8099/wordpresscrud/deletedbfetch";

      const config = {
        headers: { "content-type": "multipart/form-data" },
      };
  
      this.setState((state) => ({
        isProcessing: !state.isProcessing,
      }));
  
      axios
        .post(url, form_data, config)
        .then((res) => {
          console.log(res.data);
          this.setState((state) => ({
            isProcessing: !state.isProcessing,
            alertcolor: "success",
            isalert: true,
            alertmsg: "Great Job! Created Successfully",
          }));
        })
        .catch((err) => {
          alert(err);
          this.setState((state) => ({
            isProcessing: !state.isProcessing,
            alertcolor: "danger",
            isalert: true,
            alertmsg: "Something Went Wrong Check Form Fields",
          }));
        });




  }
 

  render() {
    const isProcessing = this.state.isProcessing;
    const its = this.state.images.map((item, key) => {
      return <tr key={item.postid}>
        <td>
          <div className="custom-control custom-control-alternative custom-checkbox">
            <input
              className="custom-control-input"
              type="checkbox"
              id={item.postid}
              name='allchildren' checked={this.state.checkedItems.get(item.postid)}
              onChange={this.state.handleChange}
            />
            <label
              className="custom-control-label"
              htmlFor={item.postid}
              style={{ marginBottom: "12px" }}
            >

            </label>
          </div>

        </td>
        <td><a href={item.link}>{item.title}</a></td>

        <td>{item.status}</td>
        <td>{item.url}</td>

      </tr>
    });
    return (
      <>
        <Header />
        {/* Page content */}

        <Container className="mt--7 mb--3" fluid>
          <Card>
            <CardHeader>
              <p>Wordpress YouTube Dashboard</p>
            </CardHeader>
            <CardBody>
              <div className="nav-wrapper">
                <Nav
                  className="nav-fill flex-column flex-md-row"
                  id="tabs-icons-text"
                  pills
                  role="tablist"
                >
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 1}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 1,
                      })}
                      onClick={(e) => this.toggleNavs(e, "tabs", 1)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-cloud-upload-96 mr-2" />
                      Add
                    </NavLink>
                  </NavItem>

                </Nav>
              </div>
              <Card className="shadow">
                <CardBody>
                  <TabContent activeTab={"tabs" + this.state.tabs}>
                    <TabPane tabId="tabs1">
                      <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ? (
                              <Alert
                                className={`alert-${this.state.alertcolor}`}
                                fade={true}
                              >
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong>{" "}
                                  {this.state.alertmsg}
                                </span>
                              </Alert>
                            ) : null}
                          </Col>
                        </Row>

                        <Row>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Enter Website : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="text"
                                  name="sitename"
                                  id="sitename"
                                  placeholder="Enter Website With http"
                                  value={this.state.sitename}
                                  onChange={this.handleChange}
                                ></Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Search Word : (required for Filter Only) </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="text"
                                  name="searchword"
                                  id="searchword"
                                  placeholder="Searchword Kwd"
                                  value={this.state.searchword}
                                  onChange={this.handleChange}
                                ></Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select Type : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="select"
                                  name="selecttype2"
                                  id="selecttype2"
                                  value={this.state.selecttype2}
                                  onChange={this.handleChange}
                                >
                                  <option>Select Type</option>
                                  <option value="posts">Posts</option>
                                  <option value="pages">Pages</option>
                                  <option value="tags">Tags</option>
                                  <option value="categories">Categories</option>
                                  <option value="media">Media</option>
                                  <option value="users">Users</option>
                                  <option value="comments">Comments</option>


                                </Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>

                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Search In : (required for filter only) </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="select"
                                  name="searchin"
                                  id="searchin"
                                  value={this.state.searchin}
                                  onChange={this.handleChange}
                                >
                                  <option>Select Type</option>
                                  <option value="title">Title</option>
                                  <option value="content">Content</option>
                                  <option value="tags">Tags</option>
                                  <option value="category">Category</option>
                                  


                                </Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>
                            <Col md="6">

                                <FormGroup className="mb-3">
                                  <Label for="docxbunch">Upload .docx (For MS Word Docx Files Only)</Label>
                                  <InputGroup className="input-group-alternative">
                                  <CustomInput type="file" id="docxbunch" name="docxbunch" 
                                  onChange={this.onStaticNewFileChange} multiple label="Yo, pick docx files!" />
                                  </InputGroup>
                                </FormGroup>

                                </Col>
                        </Row>
                        <div className="text-center">
                        
                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleSubmit}
                            className="my-4"
                            color="primary"
                            type="button"
                          >
                            Submit
                          </Button>
                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleSubmitTable}
                            className="my-4"
                            color="primary"
                            type="button"
                            id = "resetclass"
                          >
                            Get Data
                          </Button>

                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleFilteredTable}
                            className="my-4"
                            color="primary"
                            type="button"
                            id = "resetclass"
                          >
                            Filter
                          </Button>
                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleDocx}
                            className="my-4"
                            color="primary"
                            type="button"
                          >
                            Parse Docx
                          </Button>


                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                      </Form>
                    </TabPane>

                  </TabContent>
                  <Col>
                    <hr></hr>
                    <h3>{this.state.imagesendmsg}</h3>
                    <Row>
                      <Col md="12">
                        <FormGroup className="mb-3">
                          <Label>Enter Title : </Label>
                          <InputGroup className="input-group-alternative">
                            <Input
                              required
                              type="textarea"
                              name="titledata"
                              id="titledata"
                              placeholder="Your Title Goes Here"
                              rows="3"
                              value={this.state.titledata}
                              onChange={this.handleChange}
                            ></Input>
                          </InputGroup>
                        </FormGroup>
                      </Col>
                      <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Select Type : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="select"
                                  name="operation"
                                  id="operation"
                                  value={this.state.operation}
                                  onChange={this.handleChange}
                                >
                                  <option>Select Type</option>
                                  <option value="prep">Preppend</option>
                                  <option value="add">Append</option>
                                  <option value="substract">Replace</option>
                                  


                                </Input>
                              </InputGroup>
                            </FormGroup>

                          </Col>
                      <Col md="12">
                        <FormGroup className="mb-3">
                          <Label>Place Html Content : </Label>
                          <InputGroup className="input-group-alternative">
                            <Input
                              required
                              type="textarea"
                              name="textboxdata"
                              id="textboxdata"
                              placeholder="Html Formatted Data"
                              rows="10"
                              value={this.state.textboxdata}
                              onChange={this.handleChange}
                            ></Input>
                          </InputGroup>
                        </FormGroup>
                      </Col>
                    </Row>
                    <Button
                      className="my-2"
                      color="primary"
                      type="button"
                      onClick={this.toupdate}
                      disabled={this.state.isProcessing}
                    >
                      Update
                          </Button>
                    {isProcessing ? <Spinner color="primary" /> : null}


                    <Button disabled={this.state.isProcessing} className="my-2" color="danger" type="button" onClick={this.toRemoveLive}>
                      Delete
                          </Button>
                          
                    
                        <Button disabled={this.state.isProcessing} className="my-2" color="info" type="button" onClick={this.addtoQueue}>
                    Add To Queue
                        </Button>
                      {this.state.updatebox}

                    {(this.state.images).length} Results
                    <Table className="align-items-center table-flush" responsive>

                      <thead className="thead-light">

                        <tr>
                          <th scope="col">
                            <CustomInput type="checkbox" name="parentcheckbox" id="selectall" onChange={this.bulkprocessor} inline />
                          </th>
                          <th scope="col">Title</th>
                          <th scope="col">Status</th>
                          <th scope="col">Source</th>


                        </tr>
                      </thead>
                      <tbody>
                        {its}
                      </tbody>
                    </Table>

                  </Col>
                </CardBody>
                <CardFooter className="py-4">
                  <nav aria-label="...">
                    <Pagination
                      className="pagination justify-content-end mb-0"
                      listClassName="justify-content-end mb-0"   
                      
                      >

                      <PaginationItem>
                        <PaginationLink
                        id = "backiterated"                          
                          onClick={this.handleSubmitTable}
                        >
                          <i className="fas fa-angle-right" />
                          <span className="sr-only">Back</span>
                        </PaginationLink>
                      </PaginationItem>
                      
                      <PaginationItem className="active">
                        <PaginationLink                          
                          id = "resetclass"
                          onClick={this.handleSubmitTable}
                        >
                          1
                        </PaginationLink>
                      </PaginationItem>                      
                      
                      <PaginationItem>
                        <PaginationLink
                        id = "iteratedpages"                          
                          onClick={this.handleSubmitTable}
                        >
                          <i className="fas fa-angle-right" />
                          <span className="sr-only">Next</span>
                        </PaginationLink>
                      </PaginationItem>
                    </Pagination>
                  </nav>
                </CardFooter>
              </Card>
            </CardBody>
          </Card>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
    username: state.login.username,
  };
};

export default connect(mapStateToProps)(wordpressGet);
