
import React from "react";
import axios from "axios";
// import { Link } from "react-router-dom";
// reactstrap components
import DjangoCSRFToken from 'django-react-csrftoken'
import {
    Card, Container, Button, Row, 

    Badge,
    CardFooter,
    CardHeader,
    Pagination,
    PaginationItem,
    PaginationLink,
    Table,
    Spinner,
    CustomInput
} from "reactstrap";

import StyledDropzone from "views/examples/subcomponents/uploadform.js"
// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}
import { connect } from "react-redux";
import ReactPlayer from 'react-player'
class CreatedVideos extends React.Component {
    

    constructor(props) {
        super(props);        
        
        this.state = {
            title: 0,
            author: this.props.username,
            foldername: '',
            articles: [],
            checkedvalues : [],
            checkedItems: new Map(),
            isProcessing:false,
            checked:false,
            musicfiles:[],
            musticstatus:false
            
        };
        this.bulkprocessor = this.bulkprocessor.bind(this);
    }

    


    fetchArticles = (foldername) => {
        axios.get(`http://localhost:8099/home/${this.state.author}/${foldername}/videos`).then(res => {

            this.setState({
                articles: res.data.response,
                foldername: foldername
            });
        });
        

    }

    componentDidMount() {
        const foldername = this.props.match.params.foldername       
        
        this.fetchArticles(foldername);
    }

    onFileChange = (e) => {
        this.setState({ "musicfiles": e.target.files })
        console.log((e.target.files).length);
        if ((e.target.files).length >= 1){
            this.setState({
                musticstatus: !this.state.musticstatus
                
            });
        
        }
      }

    handleDelete = (item) => {
                         
        
        const articles = this.state.articles.filter(i => i.id !== item.id);
        this.setState({ articles });
        
        const config = {
            headers: { 'Content-Type': 'application/json' }
        }
        axios.post(`http://localhost:8099/home/${this.state.author}/${this.state.foldername}/deletes`,item.id,config).then(res => {
            alert(res.data);
        });

    }


    bulkprocessor=async(e)=>{
        var allchecks = [];
        let i;
        allchecks = document.getElementsByName('childcheckbox');

        if (e.target.id === 'selectall'){
            if (e.target.checked===true){
                
                // document.getElementById('idhere').checked = false;
                 for (i of allchecks){
                     console.log(i.id);
                     document.getElementById(i.id).checked = true;

                 }

            }
            else{

                for (i of allchecks){
                    console.log(i.id);
                    document.getElementById(i.id).checked = false;

                }

            }
            // console.log(e.target.checked);

        }

        else if (e.target.id === 'bulkdelete'){
            let truebox = []
            
            
            // for (let [key,value] of Object.entries(allchecks)) {
            //     console.log(key,value.);
            // }
            Object.entries(allchecks).filter(([key,val]) => {
            if (val.checked === true){
                truebox.push(val.id)
                let articles = this.state.articles.filter(i => i.id !== val.id);
                this.setState({ articles });
            }
            
            return truebox
            })
            console.log(truebox);

            
            
            // let form_data = new FormData();
            // for (const key of Object.keys(this.state.musicfiles)) {
            //     form_data.append('musicfiles', this.state.musicfiles[key])
            // }
            // form_data.append('image', this.state.musicfiles, this.state.musicfiles.name);

            // form_data.append('piclist', truebox);

            const config = {
                headers: { 'Content-Type': 'application/json' }
            }
            axios.post(`http://localhost:8099/bulk/${this.state.author}/${this.state.foldername}/videos`,truebox,config).then(res => {
                alert(res.data);
            });
            
            

        }

        else if (e.target.id === 'bulkmusic'){
            let truebox = []
            
            
            // for (let [key,value] of Object.entries(allchecks)) {
            //     console.log(key,value.);
            // }
            Object.entries(allchecks).filter(([key,val]) => {
            if (val.checked === true){
                truebox.push(val.id)
                
            }
            
            return truebox
            })
            console.log(truebox);

            
            
            let form_data = new FormData();
            for (const key of Object.keys(this.state.musicfiles)) {
                form_data.append('musicfiles', this.state.musicfiles[key])
            }
            // form_data.append('image', this.state.musicfiles, this.state.musicfiles.name);

            form_data.append('piclist', truebox);

            const config = {
                headers: { 'Content-Type': 'application/json' }
            }
            axios.post(`http://localhost:8099/bulk/${this.state.author}/${this.state.foldername}/music`,form_data,config).then(res => {
                alert(res.data);
            });
            
            

        }

        else if (e.target.id === 'combine'){
            let truebox = []
            
            
            // for (let [key,value] of Object.entries(allchecks)) {
            //     console.log(key,value.);
            // }
            Object.entries(allchecks).filter(([key,val]) => {
            if (val.checked === true){
                truebox.push(val.id)
                
            }
            
            return truebox
            })
            console.log(truebox);

            
            
            let form_data = new FormData();
            for (const key of Object.keys(this.state.musicfiles)) {
                form_data.append('musicfiles', this.state.musicfiles[key])
            }
            // form_data.append('image', this.state.musicfiles, this.state.musicfiles.name);

            form_data.append('piclist', truebox);

            const config = {
                headers: { 'Content-Type': 'application/json' }
            }
            axios.post(`http://localhost:8099/bulk/${this.state.author}/${this.state.foldername}/combine`,form_data,config).then(res => {
                alert(res.data);
            });
            
            

        }
        
        
        

    }



    render() {

        const items = this.state.articles.map((item, key) =>
            <tr key={item.source}>
                 <td><CustomInput inline
                    id={item.source}
                    type="checkbox"
                    name='childcheckbox' checked={this.state.checkedItems.get(item.name)}
                    onChange={this.handleChange}>


                    </CustomInput>
                </td>
                
                <td>
                    {(item.source).toString().includes('.mp4') ?
                    <ReactPlayer controls url={`http://localhost:8099/static/${this.state.author}/${this.state.foldername}/${item.source}`} playing />
                    
                    :
                        <img alt="..."
                        style = {{width: "50%"}}                        
                        //src={require("assets/img/"+this.state.author+"/"+this.state.foldername+"/"+item.source)}
                        src = {`http://localhost:8099/static/${this.state.author}/${this.state.foldername}/${item.source}`}
                        />
                        


                    }
                   
                 </td>
                <td>
                    <Badge color="" className="badge-dot mr-4">
                        <i className="bg-warning" />
                        {item.author}
                    </Badge>
                </td>
                <td>

                    <Button id={item.source} onClick={this.handleDelete.bind(this, item)} className="my-2" color="danger" type="button">
                        Delete
                    </Button>
                </td>


            </tr>
        )

        return (
            <>
                <Header />
                {/* Page content */}

                <Container className="mt--7" fluid>

                    <Row>


                        <div className="col">
                            <Card className="shadow">
                                <CardHeader className="border-0">
                                    <h3 className="mb-0">Card tables</h3>
                                    <Button disabled={this.state.isProcessing} onClick={this.bulkprocessor} className="my-4" id="bulkdelete" color="danger" type="button">
                                        Delete
                                    </Button>
                                    
                                    
                                        
                                        <CustomInput accept=".mp3" label="Select Sound clip to Add Music" name="musicfiles" id="musicfiles" onChange={this.onFileChange} type="file" ></CustomInput>

                                    {this.state.musticstatus ?
                                    <span>
                                    <Button disabled={this.state.isProcessing} onClick={this.bulkprocessor} className="my-4" id="bulkmusic" color="info" type="button">
                                        Add Music to Videos
                                    </Button>
                                    
                                    <Button disabled={this.state.isProcessing} onClick={this.bulkprocessor} className="my-4" id="combine" color="secondary" type="button">
                                        Combine Videos
                                    </Button>
                                    </span>
                                    :
                                    null
                                    }
                                    
                                </CardHeader>
                                <Table className="align-items-center table-flush" responsive>
                                    <thead className="thead-light">
                                        <tr>
                                        <th scope="col"><CustomInput type="checkbox" name="parentcheckbox" id="selectall" onChange={this.bulkprocessor} inline />  </th>                                    
                                            <th scope="col">Source</th>
                                            <th scope="col">Author</th>
                                            <th scope="col">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {items}
                                    </tbody>
                                </Table>
                                <CardFooter className="py-4">
                                    <nav aria-label="...">
                                        <Pagination
                                            className="pagination justify-content-end mb-0"
                                            listClassName="justify-content-end mb-0"
                                        >
                                            <PaginationItem className="disabled">
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                    tabIndex="-1"
                                                >
                                                    <i className="fas fa-angle-left" />
                                                    <span className="sr-only">Previous</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem className="active">
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    1
                        </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    2 <span className="sr-only">(current)</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    3
                        </PaginationLink>
                                            </PaginationItem>
                                            <PaginationItem>
                                                <PaginationLink
                                                    href="#pablo"
                                                    onClick={e => e.preventDefault()}
                                                >
                                                    <i className="fas fa-angle-right" />
                                                    <span className="sr-only">Next</span>
                                                </PaginationLink>
                                            </PaginationItem>
                                        </Pagination>
                                    </nav>
                                </CardFooter>
                            </Card>
                        </div>
                    </Row>
                </Container>

            </>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
      username: state.login.username
    };
  }


export default connect(mapStateToProps)(CreatedVideos);