import React from 'react';
import {
 Button
} from "reactstrap";


function GallaryImages(props) {

  const its = props.ims.map((item, key) => {
    return <tr key={item.id}>
         <td>
                    <div className="custom-control custom-control-alternative custom-checkbox">
                    <input
                    className="custom-control-input"
                    type="checkbox"
                    id={item.id}
                    name='allchildren' checked={props.checkedItems.get(item.id)} 
                    onChange={props.handleChange}
                  />
                  <label
                    className="custom-control-label"
                    htmlFor={item.id}
                    style={{marginBottom:"12px"}}
                  >
                    
                  </label>
                    </div>
                  
                </td>
                <td></td>
                <td></td>
        <td><img style={{width: '90%'}} alt=".." src={`http://localhost:8099/${item.image}`}/></td>        
        
      </tr>

  })



  return (
    <>
      {its}

    </>

  );
}

export default GallaryImages;