
import React from "react";
import axios from "axios";
import { connect } from "react-redux";
// import canvasToImage from 'canvas-to-image';
// reactstrap components

import {
    Card, Container, Button, Row, FormGroup,
    Form,
    // Badge,
    // CardFooter,
    Input,
    // InputGroupAddon,
    // InputGroupText,
    InputGroup,
    CardHeader,
    CardBody,
    Label,
    CustomInput,
    CardImg,
    UncontrolledAlert,
    Col,
    Spinner
} from "reactstrap";


// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}

class ImageWalker extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            title: 'YouTubeV.jpg',
            author: this.props.username,            
            articles: [],
            isProcessing: false,
            background: '',
            font: 'Arial',
            multirefiles: '',
            staticnewbg: '',
            alertcolor: null,
            isalert: false,
            alertmsg: '',
            bgpuburty: 0,
            pictype: 'YouTubeV',
            textboxdata: '',
            imagesnore:[],
            canva_w:640,
            canva_h:320,
            fontweight:'normal',
            // customfont:0,
            customfontposition:0,
            // customfontwidth:0


        };
        
        
    }


    fetchArticles = async () => {
        await axios.get("http://localhost:8099/customimage/curatedrefs").then(res => {

            this.setState({
                articles: res.data.single
            });
        });
        console.log(this.state.articles);
    }

    componentDidMount() {
        this.fetchArticles();       
   

        const token = localStorage.getItem('token');
        console.log(token)
        if (!token) {
            this.props.history.push('login');
            // this.props.history.push('register');       

        }
        else {
            this.props.history.push('img-walker');

            //  console.log(this.props)    

        }

    }

   

    myFunction = async () => {
        var determinpic = this.state.background;
        var img = new Image();   
        var i = 0;     
        
        var textboxdata = this.state.textboxdata;
        let canvasitems = textboxdata.split('\n'); 
        
        if (determinpic === 'static'){
            img.src = `http://localhost:8099/static/curatedrefs/${this.state.title}`;
            
            for (i; i < canvasitems.length;i++){
                console.log(i);             
                var breaks = document.createElement("br");
                var element = document.createElement("canvas");
                var appender = document.getElementById('imageappender');                
                element.setAttribute("name", "canvas");
                element.setAttribute("id", "canvas" + i);
                element.setAttribute("width", img.width);
                element.setAttribute("height", img.height);    
                element.setAttribute("crossOrigin", 'anonymous');          
                appender.append(element);
                appender.appendChild(breaks);

            }
    }
    else {
        var imagetitles = [...this.state.imagesnore];
        console.log(canvasitems.length);
        i=0;
        for (i; i < canvasitems.length;i++){
            var breaks2 = document.createElement("br");
            var element2 = document.createElement("canvas");
            var appender2 = document.getElementById('imageappender');            
            img.src = `http://localhost:8099/static/${this.state.author}/reference_backs/${imagetitles[0]}`;
            console.log(img.src);
            console.log(this.state.canva_w,this.state.canva_h);
            element2.setAttribute("name", "canvas");
            element2.setAttribute("id", "canvas" + i);
            element2.setAttribute("width", this.state.canva_w);
            element2.setAttribute("height", this.state.canva_h);
            appender2.append(element2);
            appender2.appendChild(breaks2);

        }


    }


    }



    Textrender = async () => {        
        var i = 0;
        var determinpic = this.state.background;
        var img = new Image();
        var imagetitles = [...this.state.imagesnore];
        var textboxdata = this.state.textboxdata;
        var canvasitems = textboxdata.split('\n');  
        
        
        for (i; i < canvasitems.length;i++){

            var canvas = document.getElementById("canvas" + i);
            var ctx = canvas.getContext("2d");
            var ox = canvas.width / 2;
            var oy = canvas.height / 2;
            // var img = document.getElementsByClassName('card-img-top')[0].src
            if (determinpic === 'static'){
                img.src = `http://localhost:8099/static/curatedrefs/${this.state.title}`;
                
            }
            else{

                img.src = `http://localhost:8099/static/${this.state.author}/reference_backs/${imagetitles[Math.floor(Math.random() * imagetitles.length)]}`;

            }
            
            ctx.fillStyle = "white";
            ctx.textAlign = "center";
            //var textdata = "యూట్యూబ్ వాచ్‌టైమ్‌లో 70% కంటే ఎక్కువ"
            await this.Textwrap(canvasitems[i], ctx, ox, oy,img)


        }
    }

    fontwrap = (splitted) => {
        var fontsize = 60
        let oyadjust = 0
        if (this.state.pictype === 'YouTubeV') {

            if (splitted.length <= 3) {
                fontsize = this.state.fontweight+" 150px "+this.state.font;
            }
            else if (splitted.length > 3 && splitted.length <= 6) {
    
                fontsize = this.state.fontweight+" 65px "+this.state.font;
    
            }
            else if (splitted.length > 6 && splitted.length <= 10) {
    
                fontsize = this.state.fontweight+" 60px "+this.state.font;
    
            }
            else {
    
                fontsize = this.state.fontweight+" 60px "+this.state.font;
    
            }

        }

     else if (this.state.pictype === 'FacebookV'){

        if (splitted.length <= 3) {
            fontsize = this.state.fontweight+" 150px "+this.state.font;
        }
        else if (splitted.length > 3 && splitted.length <= 6) {

            fontsize = this.state.fontweight+" 65px "+this.state.font;

        }
        else if (splitted.length > 6 && splitted.length <= 10) {

            fontsize = this.state.fontweight+" 60px "+this.state.font;

        }
        else {

            fontsize = this.state.fontweight+" 60px "+this.state.font;

        }


     }
     else if (this.state.pictype === "TiktokS"){

        if (splitted.length <= 3) {
            fontsize = this.state.fontweight+" 120px "+this.state.font;
        }
        else if (splitted.length > 3 && splitted.length <= 6) {

            fontsize = this.state.fontweight+" 55px "+this.state.font;

        }
        else if (splitted.length > 6 && splitted.length <= 10) {

            fontsize = this.state.fontweight+" 50px "+this.state.font;
            oyadjust = 100;

        }
        else {

            fontsize = this.state.fontweight+" 50px "+this.state.font;
            oyadjust = 100;

        }


     }
     else if (this.state.pictype === "FacebookS"){

        if (splitted.length <= 3) {
            fontsize = this.state.fontweight+" 120px "+this.state.font;
        }
        else if (splitted.length > 3 && splitted.length <= 6) {

            fontsize = this.state.fontweight+" 55px "+this.state.font;

        }
        else if (splitted.length > 6 && splitted.length <= 10) {

            fontsize = this.state.fontweight+" 50px "+this.state.font;
            oyadjust = 100;

        }
        else {

            fontsize = this.state.fontweight+" 50px "+this.state.font;
            oyadjust = 100;

        }


     }
     else {

        if (splitted.length <= 3) {
            fontsize = this.state.fontweight+" 120px "+this.state.font;
        }
        else if (splitted.length > 3 && splitted.length <= 6) {

            fontsize = this.state.fontweight+" 55px "+this.state.font;

        }
        else if (splitted.length > 6 && splitted.length <= 10) {

            fontsize = this.state.fontweight+" 50px "+this.state.font;
            oyadjust = 100;

        }
        else {

            fontsize = this.state.fontweight+" 50px "+this.state.font;
            oyadjust = 100;

        }

     }
        
        return [fontsize,oyadjust]
    }

    Textwrap = (textdata, ctx, ox, oy, img) => {
        var chunk = [];
        var fontsize = this.state.customfont;
        var textwidth = Math.round(ctx.measureText(textdata).width);
        console.log(textwidth);
        var splitted = textdata.split(' ');
        ctx.drawImage(img, 1, 1);
        var oyadjust = 0;
        [fontsize , oyadjust] = this.fontwrap(splitted)
        // if (this.state.customfont > 0) {
                
        //     fontsize = this.state.customfont;
        // }
        ctx.font = fontsize;

        

        while (splitted.length > 0) {

            if (this.state.pictype === 'YouTubeV'){

                if (splitted.length <= 3) {

                    chunk = splitted.splice(0, 3);
                }
                else if (splitted.length > 3 && splitted.length <= 6) {
    
                    chunk = splitted.splice(0, 4);
    
                }
                else if (splitted.length > 6 && splitted.length <= 10) {
    
                    chunk = splitted.splice(0, 5);
    
                }
                else {
    
                    chunk = splitted.splice(0, 5);
    
                }

            }

            else if (this.state.pictype === 'FacebookV'){

                if (splitted.length <= 3) {

                    chunk = splitted.splice(0, 2);
                }
                else if (splitted.length > 3 && splitted.length <= 6) {
    
                    chunk = splitted.splice(0, 2);
    
                }
                else if (splitted.length > 6 && splitted.length <= 10) {
    
                    chunk = splitted.splice(0, 4);
    
                }
                else {
    
                    chunk = splitted.splice(0, 3);
    
                }

            }

            else if (this.state.pictype === 'TiktokS'){

                if (splitted.length <= 3) {

                    chunk = splitted.splice(0, 2);
                }
                else if (splitted.length > 3 && splitted.length <= 6) {
    
                    chunk = splitted.splice(0, 2);
    
                }
                else if (splitted.length > 6 && splitted.length <= 10) {
    
                    chunk = splitted.splice(0, 4);
    
                }
                else {
    
                    chunk = splitted.splice(0, 3);
    
                }

            }

            else if (this.state.pictype === 'FacebookS'){

                if (splitted.length <= 3) {

                    chunk = splitted.splice(0, 2);
                }
                else if (splitted.length > 3 && splitted.length <= 6) {
    
                    chunk = splitted.splice(0, 2);
    
                }
                else if (splitted.length > 6 && splitted.length <= 10) {
    
                    chunk = splitted.splice(0, 4);
    
                }
                else {
    
                    chunk = splitted.splice(0, 3);
                    
    
                }            

            }

            else {
                if (splitted.length <= 3) {

                    chunk = splitted.splice(0, 2);
                }
                else if (splitted.length > 3 && splitted.length <= 6) {
    
                    chunk = splitted.splice(0, 2);
    
                }
                else if (splitted.length > 6 && splitted.length <= 10) {
    
                    chunk = splitted.splice(0, 4);
    
                }
                else {
    
                    chunk = splitted.splice(0, 3);
                    
    
                } 
                
                

            }
            
            if (this.state.customfontposition > 1) {

                oyadjust = this.state.customfontposition;
            }
            
            

            ctx.fillText(chunk.join(' '), ox, oy-oyadjust);
            console.log(ctx.measureText(chunk.join(' ')).width)
            oy += 100;

        }
    }

    onFileChange = (e) => {
        this.setState({ "multirefiles": e.target.files })
      }

    onStaticNewFileChange = (e) => {
        this.setState({ "staticnewbg": e.target.files })
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value

        });
    }

    CanvaReset = (e) => {
        var i = "";
        var canvas = document.getElementsByTagName("canvas");
        
        for (i of canvas){ 
            i.remove();
        }


    }

    download_img = async () => {
        var i = "";
        var canvas = document.getElementsByTagName("canvas");
        
        for (i of canvas){               
            

            var link = document.createElement('a');
            link.download = 'filename.png';
            link.href = i.toDataURL("image/png", 1.0);
            link.click();
        //    await canvasToImage(i,{
        //     name: 'filename',
        //     type: 'png',
        //     quality: 1
        //   });
           

        }
    }

   

    

    handleSubmit = (e) => {
        e.preventDefault();

        let form_data = new FormData();
        
        form_data.append('title', this.state.title);
        form_data.append('author', this.state.author);
        
        
        if (this.state.multirefiles) {
            for (const key of Object.keys(this.state.multirefiles)) {
                form_data.append('multirefiles', this.state.multirefiles[key])
                this.state.imagesnore.push((this.state.multirefiles[key].name).replace(" ", "_"))
                               
            }
            
        }
        
        
        if (this.state.staticnewbg) {
            for (const key of Object.keys(this.state.staticnewbg)) {
                form_data.append('staticnewbg', this.state.staticnewbg[key])
                this.state.imagesnore.push((this.state.staticnewbg[key].name).replace(" ", "_"))
                this.setState(state => ({
                    title:  (state.staticnewbg[key].name).replace(" ", "_")
                }));
                

            }
        }
        
        
        let url = 'http://localhost:8099/imgwalk/';
        const config = {
            headers: { 'content-type': 'multipart/form-data' }
        }
        this.setState(state => ({
            isProcessing: !state.isProcessing
        }));
        axios.post(url, form_data, config)
            .then(res => {
                console.log(res.data);
                this.setState(state => ({
                    canva_h:res.data.height,
                    canva_w:res.data.width,
                    isProcessing: !state.isProcessing,
                    alertcolor: 'success',
                    isalert: true,
                    alertmsg: 'Great Job Images Created Successfully, Check Folders'

                }));
            })
            .catch(err => {
                alert(err)
                this.setState(state => ({
                    isProcessing: !state.isProcessing,
                    alertcolor: 'danger',
                    isalert: true,
                    alertmsg: 'Something Went Wrong Check Form Fields'
                }));

            })
    };






    render() {

        const items = this.state.articles.map((item, key) =>

            <option key={item.source} value={item.source}>{item.source}</option>


        )
        const isProcessing = this.state.isProcessing;
        return (
            <>
                <Header />
                {/* Page content */}

                <Container className="mt--7" fluid>
                    <Row>
                        <div className="col">
                            <Card className="shadow border-0">
                                <CardHeader>
                                    <p>What do You Want to Write ?</p>
                                    
                                </CardHeader>
                                <CardBody>
                                    <Form role="form">
                                        <Row>

                                            <Col md="4">
                                                <FormGroup className="mb-3">
                                                    <Label>Slect Backgrond</Label>
                                                    <InputGroup className="input-group-alternative">

                                                        <Input type="select" name="title" id="title" value={this.state.title} onChange={this.handleChange}>

                                                            {items}

                                                        </Input>
                                                    </InputGroup>
                                                </FormGroup>
                                                <FormGroup className="mb-3">
                                                    <Card style={{ width: "22rem" }}>

                                                        <CardImg top style={{ width: '22rem' }} alt="..." src={`http://localhost:8099/static/curatedrefs/${this.state.title}`} />

                                                    </Card>
                                                </FormGroup>
                                            </Col>
                                        OR
                                        <Col md="6">

                                                <FormGroup className="mb-3">
                                                    <Label for="newbgstatic">Upload New Background</Label>
                                                    <InputGroup className="input-group-alternative">
                                                        <CustomInput type="file" id="newbgstatic" name="newbgstatic"
                                                            onChange={this.onStaticNewFileChange} label="Yo, pick a file!" />
                                                    </InputGroup>
                                                </FormGroup>

                                            </Col>
                                        </Row>

                                        <FormGroup className="mb-3">
                                            <Label>Select Image Type</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="select" name="pictype" id="pictype" value={this.state.pictype} onChange={this.handleChange}>
                                                    <option value="custom">Custom</option>
                                                    <option value="YouTubeV">YouTube Video</option>
                                                    <option value="InstagramS">Instragram Story</option>
                                                    <option value="FacebookS">Facebook Story</option>
                                                    <option value="FacebookV">Facebook Video</option>
                                                    <option value="TwitterV">Twitter Video</option>
                                                    <option value="TiktokS">Tiktok Story</option>
                                                    <option value="WhatsappS">Whatsapp Story</option>

                                                </Input>
                                            </InputGroup>
                                        </FormGroup>


                                        <FormGroup className="mb-3">
                                            <Label>Type Backgrond</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="select" name="background" id="background" value={this.state.background} onChange={this.handleChange}>
                                                    <option value=''>Select Background</option>
                                                    <option value="static">Static Background</option>
                                                    <option value="dynamic">Dynamic Background</option>
                                                </Input>
                                            </InputGroup>
                                        </FormGroup>
                                        {/* <FormGroup className="mb-3">
                                            <Label>Custom Font</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="number" name="customfont" id="customfont" value={this.state.customfont} onChange={this.handleChange}>
                                                    
                                                </Input>
                                            </InputGroup>
                                        </FormGroup> */}
                                        <FormGroup className="mb-3">
                                            <Label>Custom Font Position</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="number" name="customfontposition" id="customfontposition" value={this.state.customfontposition} onChange={this.handleChange}>
                                                    
                                                </Input>
                                            </InputGroup>
                                        </FormGroup>
                                        {/* <FormGroup className="mb-3">
                                            <Label>Custom Font Width</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="number" name="customfontwidth" id="customfontwidth" value={this.state.customfontwidth} onChange={this.handleChange}>
                                                    
                                                </Input>
                                            </InputGroup>
                                        </FormGroup> */}

                                        <FormGroup className="mb-3">
                                            <Label>Slect Font Style</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="select" name="font" id="font" value={this.state.font} onChange={this.handleChange}>
                                                    <option value=''>Select Font Style</option>
                                                    <option value="Ramabhadra">RamaBhadra</option>
                                                    <option value="Rubik">Rubik</option>
                                                    <option value="Damion">Damion</option>
                                                    <option value="Verdana">Verdana</option>
                                                    <option value="Times New Roman">Times New Roman</option>
                                                    <option value="Century Gothic">Century Gothic</option>
                                                    <option value="Oswald">Oswald</option>                                                    
                                                    <option value="Courier New">Courier New</option>
                                                    <option value="serif">serif</option>
                                                    <option value="sans-serif">sans-serif</option>
                                                </Input>
                                            </InputGroup>
                                        </FormGroup>
                                                                               
                                        <FormGroup className="mb-3">
                                            <Label>Slect Font Weight</Label>
                                            <InputGroup className="input-group-alternative">

                                                <Input type="select" name="fontweight" id="fontweight" value={this.state.fontweight} onChange={this.handleChange}>
                                                    <option value=''>Select Font Weight</option>
                                                    <option value="normal">default</option>
                                                    <option value="400">400</option>
                                                    <option value="500">500</option>
                                                    <option value="600">600</option>
                                                    <option value="700">700</option>
                                                    <option value="800">800</option>
                                                    <option value="900">900</option>
                                                    
                                                </Input>
                                            </InputGroup>
                                        </FormGroup>
                                        
                                        {/* <FormGroup className="mb-3">
                  <Label>Upload Bg Files</Label>
                  <InputGroup className="input-group-alternative">
                    <Input type="file" id="multirefiles" name="multirefiles" onChange={this.onFileChange} multiple />                    
                  </InputGroup>
                </FormGroup> */}


                                        <FormGroup className="mb-3">
                  <Label for="multirefiles">Upload Bg Files</Label>
                  <InputGroup className="input-group-alternative">
                  <CustomInput type="file" id="multirefiles" name="multirefiles" 
                  onChange={this.onFileChange} multiple label="Yo, pick a files!" />
                  </InputGroup>
                </FormGroup>

                                        <FormGroup className="mb-3">
                                            <Label>Place Text By Enter : </Label>
                                            <InputGroup className="input-group-alternative">
                                                <Input
                                                    required
                                                    type="textarea"
                                                    name="textboxdata"
                                                    id="textboxdata"
                                                    placeholder="Your Data Categorized by Enter"
                                                    rows="10"
                                                    value={this.state.textboxdata}
                                                    onChange={this.handleChange}
                                                ></Input>
                                            </InputGroup>
                                        </FormGroup>



                                        {this.state.isalert ?
                                            <UncontrolledAlert className={`alert-${this.state.alertcolor}`} fade={true}>
                                                <span className="alert-inner--icon">
                                                    <i className="ni ni-like-2" />
                                                </span>{" "}
                                                <span className="alert-inner--text">
                                                    <strong>Status !</strong> {this.state.alertmsg}
                                                </span>
                                            </UncontrolledAlert>
                                            :
                                            null
                                        }

                                        <div className="text-center">
                                            <Button disabled={this.state.isProcessing} onClick={this.handleSubmit} className="my-4" color="primary" type="button">
                                                Submit
                  </Button>

                                            <Button disabled={this.state.isProcessing} onClick={this.myFunction} className="my-4" color="info" type="button">
                                                Generate
                  </Button>
                                            <Button disabled={this.state.isProcessing} onClick={this.Textrender} className="my-4" color="default" type="button">
                                                Textrender
                  </Button>
                  <Button disabled={this.state.isProcessing} onClick={this.download_img} className="my-4" color="secondary" type="button">
                                                Download
                  </Button>
                  <Button disabled={this.state.isProcessing} onClick={this.CanvaReset} className="my-4" color="danger" type="button">
                                                Reset
                  </Button>

                                            {isProcessing ? <Spinner color="primary" /> : null}
                                        </div>
                                    </Form>
                                </CardBody>


                            </Card>
                        </div>
                    </Row>

                    <Row>



                    </Row>
                    <Row>
                        <Col>
                            <div id="imageappender">

                            </div>
                        </Col>
                    </Row>
                </Container>

            </>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
        //   token: state.login.token,
        //   status: state.login.status,
        //   spinning: state.login.spinning,
        username: state.login.username
    };
}

export default connect(mapStateToProps)(ImageWalker);