import React from "react";
import axios from "axios";
import { connect } from "react-redux";
import classnames from "classnames";

// reactstrap components
import {
  Card,
  Container,
  Button,
  Row,
  FormGroup,
  Form,
  Input,
  InputGroup,
  CardHeader,
  CardBody,
  Label,
  CustomInput,
  Alert,
  Col,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  Spinner,
} from "reactstrap";

// core components
import Header from "components/Headers/HeaderCustom.js";
// mapTypeId={google.maps.MapTypeId.ROADMAP}

class Statscraper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "group",
      author: this.props.username,
      tabs: 1,
      groupdata: [],
      isProcessing: false,
      alertcolor: null,
      isalert: false,
      alertmsg: "",
      textboxdata: "",
    };
    this.handleDownload = this.handleDownload.bind(this);
  }

  // fetchArticles = async () => {
  //   await axios.get("http://localhost:8099/telegram/add/").then(res => {

  //     this.setState({
  //       groupdata: res.data.group,
  //       channeldata: res.data.channels,
  //     });
  //   });
  //   console.log(this.state.groupdata);
  // }

  toggleNavs = (e, state, index) => {
    e.preventDefault();
    this.setState({
      [state]: index,
    });
  };

  componentDidMount() {
    const token = localStorage.getItem("token");
    console.log(token);
    if (!token) {
      this.props.history.push("login");
      // this.props.history.push('register');
    } else {
      this.props.history.push("scrape");

      //  console.log(this.props)
    }
  }

  // getfilename = (e)=>{
  //   return e;
  // }

  handleDelete = (e) => {
    // const articles = this.state.articles.filter(i => i.id !== item.id)
    // this.setState({ articles })
    // console.log(this.state);
    e.preventDefault();

    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));
    const config = {
      headers: { "Content-Type": "application/json" },
      responseType: "blob",
    };
    axios.delete(`http://localhost:8099/scrape/`, config).then((res) => {
      console.log(res);

      this.setState((state) => ({
        isProcessing: !state.isProcessing,
      }));
    });
  };

  onFileChange = (e) => {
    this.setState({ multirefiles: e.target.files });
  };

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    let form_data = new FormData();

    form_data.append("textboxdata", this.state.textboxdata);
    if (!this.state.titile & !this.state.textboxdata) {
      return this.setState((state) => ({
        alertcolor: "danger",
        isalert: true,
        alertmsg: "Something Went Wrong Check Form Fields",
      }));
    }
    let url = "http://localhost:8099/scrape/";

    const config = {
      headers: { "content-type": "multipart/form-data" },
    };
    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));
    axios
      .post(url, form_data, config)
      .then((res) => {
        console.log(res.data);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "success",
          isalert: true,
          alertmsg: "Great Job! Created Successfully",
        }));
      })
      .catch((err) => {
        alert(err);
        this.setState((state) => ({
          isProcessing: !state.isProcessing,
          alertcolor: "danger",
          isalert: true,
          alertmsg: "Something Went Wrong Check Form Fields",
        }));
      });
  };

  handleDownload = (e) => {
    e.preventDefault();

    this.setState((state) => ({
      isProcessing: !state.isProcessing,
    }));
    const config = {
      headers: { "Content-Type": "application/json" },
      responseType: "blob",
    };
    axios.get(`http://localhost:8099/scrape/`, config).then((res) => {
      console.log(res);

      this.setState((state) => ({
        isProcessing: !state.isProcessing,
      }));
      const url = window.URL.createObjectURL(new Blob([res.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", "file.xlsx"); //or any other extension
      document.body.appendChild(link);
      link.click();
    });
  };

  render() {
    const isProcessing = this.state.isProcessing;
    return (
      <>
        <Header />
        {/* Page content */}

        <Container className="mt--7 mb--3" fluid>
          <Card>
            <CardHeader>
              <p>Scraper Dashboard</p>
            </CardHeader>
            <CardBody>
              <div className="nav-wrapper">
                <Nav
                  className="nav-fill flex-column flex-md-row"
                  id="tabs-icons-text"
                  pills
                  role="tablist"
                >
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 1}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 1,
                      })}
                      onClick={(e) => this.toggleNavs(e, "tabs", 1)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-cloud-upload-96 mr-2" />
                      Add
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      aria-selected={this.state.tabs === 2}
                      className={classnames("mb-sm-3 mb-md-0", {
                        active: this.state.tabs === 2,
                      })}
                      onClick={(e) => this.toggleNavs(e, "tabs", 2)}
                      href="#pablo"
                      role="tab"
                    >
                      <i className="ni ni-bell-55 mr-2" />
                      Send Now
                    </NavLink>
                  </NavItem>
                </Nav>
              </div>
              <Card className="shadow">
                <CardBody>
                  <TabContent activeTab={"tabs" + this.state.tabs}>
                    <TabPane tabId="tabs1">
                      <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ? (
                              <Alert
                                className={`alert-${this.state.alertcolor}`}
                                fade={true}
                              >
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong>{" "}
                                  {this.state.alertmsg}
                                </span>
                              </Alert>
                            ) : null}
                          </Col>
                        </Row>

                        <Row>
                          <Button
                            className="my-4"
                            color="primary"
                            type="button"
                            onClick={this.handleDownload}
                          >
                            Download
                          </Button>
                          <Button
                            className="my-4"
                            color="danger"
                            type="button"
                            onClick={this.handleDelete}
                          >
                            Reset
                          </Button>

                          <Col md="12">
                            <FormGroup className="mb-3">
                              <Label>Place Urls By Enter : </Label>
                              <InputGroup className="input-group-alternative">
                                <Input
                                  required
                                  type="textarea"
                                  name="textboxdata"
                                  id="textboxdata"
                                  placeholder="Your Data Categorized by Enter"
                                  rows="10"
                                  value={this.state.textboxdata}
                                  onChange={this.handleChange}
                                ></Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                        </Row>
                        <div className="text-center">
                          <Button
                            disabled={this.state.isProcessing}
                            onClick={this.handleSubmit}
                            className="my-4"
                            color="primary"
                            type="button"
                          >
                            Submit
                          </Button>

                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                      </Form>
                    </TabPane>
                    <TabPane tabId="tabs2">
                      {/* <Form role="form">
                        <Row>
                          <Col md="12">
                            {this.state.isalert ?
                              <Alert className={`alert-${this.state.alertcolor}`} fade={true}>
                                <span className="alert-inner--icon">
                                  <i className="ni ni-like-2" />
                                </span>{" "}
                                <span className="alert-inner--text">
                                  <strong>Status !</strong> {this.state.alertmsg}
                                </span>
                              </Alert>
                              :
                              null
                            }
                            <FormGroup className="mb-3">
                              <Label>I Want to Send Through:</Label>
                              <InputGroup className="input-group-alternative">

                                <Input type="select" name="sendnowtitle" id="sendnowtitle" value={this.state.sendnowtitle} onChange={this.handleChange}>
                                  <option value="group">Group</option>
                                  <option value="channel">Channel</option>

                                </Input>
                              </InputGroup>
                            </FormGroup>
                          </Col>
                        </Row>


                      
                      
                      

                     
                      
 

                       

                        


                      

                          

                        



                        <div className="text-center">
                          <Button disabled={this.state.isProcessing} onClick={this.handleSendMessage} className="my-4" color="primary" type="button">
                            Send Now
                  </Button>

                          {isProcessing ? <Spinner color="primary" /> : null}
                        </div>
                      </Form> */}
                    </TabPane>
                  </TabContent>
                </CardBody>
              </Card>
            </CardBody>
          </Card>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    //   token: state.login.token,
    //   status: state.login.status,
    //   spinning: state.login.spinning,
    username: state.login.username,
  };
};

export default connect(mapStateToProps)(Statscraper);
